___CSS_LOADER_EXPORT___.push([module.id, ".badge[data-v-dd24a064]{align-items:center;display:inline-flex;font-weight:500;gap:.25rem;white-space:nowrap}.badge-sm[data-v-dd24a064]{font-size:.75rem;padding:.25rem .5rem}.badge-lg[data-v-dd24a064]{font-size:1rem;padding:.5rem .75rem}.badge i[data-v-dd24a064]{font-size:.8em}.badge-primary[data-v-dd24a064]{background-color:#007bff;color:#fff}.badge-secondary[data-v-dd24a064]{background-color:#6c757d;color:#fff}.badge-success[data-v-dd24a064]{background-color:#28a745;color:#fff}.badge-danger[data-v-dd24a064]{background-color:#dc3545;color:#fff}.badge-warning[data-v-dd24a064]{background-color:#ffc107;color:#212529}.badge-info[data-v-dd24a064]{background-color:#17a2b8;color:#fff}.badge-light[data-v-dd24a064]{background-color:#f8f9fa;color:#212529}.badge-dark[data-v-dd24a064]{background-color:#343a40;color:#fff}", "",{"version":3,"sources":["webpack://./resources/js/components/StatusBadge.vue"],"names":[],"mappings":"AA8MA,wBAEA,kBAAA,CADA,mBAAA,CAGA,eAAA,CADA,UAAA,CAEA,kBACA,CAEA,2BACA,gBAAA,CACA,oBACA,CAEA,2BACA,cAAA,CACA,oBACA,CAEA,0BACA,cACA,CAGA,gCAEA,wBAAA,CADA,UAEA,CAEA,kCAEA,wBAAA,CADA,UAEA,CAEA,gCAEA,wBAAA,CADA,UAEA,CAEA,+BAEA,wBAAA,CADA,UAEA,CAEA,gCAEA,wBAAA,CADA,aAEA,CAEA,6BAEA,wBAAA,CADA,UAEA,CAEA,8BAEA,wBAAA,CADA,aAEA,CAEA,6BAEA,wBAAA,CADA,UAEA","sourcesContent":["<template>\r\n  <span \r\n    :class=\"badgeClasses\"\r\n    :title=\"tooltipText\"\r\n  >\r\n    <i :class=\"iconClass\" v-if=\"showIcon\"></i>\r\n    {{ displayText }}\r\n  </span>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'StatusBadge',\r\n  props: {\r\n    status: {\r\n      type: String,\r\n      required: true\r\n    },\r\n    type: {\r\n      type: String,\r\n      default: 'default' // default, pilot, club, championship, matchday\r\n    },\r\n    showIcon: {\r\n      type: Boolean,\r\n      default: true\r\n    },\r\n    size: {\r\n      type: String,\r\n      default: 'md', // sm, md, lg\r\n      validator: value => ['sm', 'md', 'lg'].includes(value)\r\n    }\r\n  },\r\n  computed: {\r\n    statusConfig() {\r\n      const configs = {\r\n        // Estados generales\r\n        active: { \r\n          class: 'success', \r\n          text: 'Activo', \r\n          icon: 'fas fa-check-circle',\r\n          tooltip: 'Estado activo'\r\n        },\r\n        inactive: { \r\n          class: 'secondary', \r\n          text: 'Inactivo', \r\n          icon: 'fas fa-times-circle',\r\n          tooltip: 'Estado inactivo'\r\n        },\r\n        pending: { \r\n          class: 'warning', \r\n          text: 'Pendiente', \r\n          icon: 'fas fa-clock',\r\n          tooltip: 'Pendiente de confirmacin'\r\n        },\r\n        cancelled: { \r\n          class: 'danger', \r\n          text: 'Cancelado', \r\n          icon: 'fas fa-ban',\r\n          tooltip: 'Cancelado'\r\n        },\r\n        completed: { \r\n          class: 'info', \r\n          text: 'Completado', \r\n          icon: 'fas fa-check',\r\n          tooltip: 'Completado exitosamente'\r\n        },\r\n        \r\n        // Estados especficos de pilotos\r\n        registered: { \r\n          class: 'primary', \r\n          text: 'Inscrito', \r\n          icon: 'fas fa-user-check',\r\n          tooltip: 'Piloto inscrito'\r\n        },\r\n        confirmed: { \r\n          class: 'success', \r\n          text: 'Confirmado', \r\n          icon: 'fas fa-check-double',\r\n          tooltip: 'Participacin confirmada'\r\n        },\r\n        \r\n        // Estados de campeonatos\r\n        draft: { \r\n          class: 'secondary', \r\n          text: 'Borrador', \r\n          icon: 'fas fa-edit',\r\n          tooltip: 'En estado de borrador'\r\n        },\r\n        published: { \r\n          class: 'primary', \r\n          text: 'Publicado', \r\n          icon: 'fas fa-eye',\r\n          tooltip: 'Publicado y visible'\r\n        },\r\n        planned: {\r\n          class: 'secondary',\r\n          text: 'Planeado',\r\n          icon: 'fas fa-clock',\r\n          tooltip: 'Campeonato planeado'\r\n        },\r\n        completed: {\r\n          class: 'primary',\r\n          text: 'Completado',\r\n          icon: 'fas fa-check',\r\n          tooltip: 'Campeonato completado'\r\n        },\r\n        cancelled: {\r\n          class: 'danger',\r\n          text: 'Cancelado',\r\n          icon: 'fas fa-times',\r\n          tooltip: 'Campeonato cancelado'\r\n        },\r\n        \r\n        // Estados de jornadas (matchdays)\r\n        scheduled: { \r\n          class: 'info', \r\n          text: 'Programada', \r\n          icon: 'fas fa-calendar',\r\n          tooltip: 'Jornada programada'\r\n        },\r\n        ongoing: { \r\n          class: 'warning', \r\n          text: 'En Curso', \r\n          icon: 'fas fa-play',\r\n          tooltip: 'Jornada en curso'\r\n        },\r\n        'in-progress': { \r\n          class: 'warning', \r\n          text: 'En Curso', \r\n          icon: 'fas fa-play',\r\n          tooltip: 'Jornada en curso'\r\n        },\r\n        completed: { \r\n          class: 'success', \r\n          text: 'Completada', \r\n          icon: 'fas fa-flag-checkered',\r\n          tooltip: 'Jornada completada'\r\n        },\r\n        finished: { \r\n          class: 'success', \r\n          text: 'Finalizada', \r\n          icon: 'fas fa-flag-checkered',\r\n          tooltip: 'Jornada finalizada'\r\n        },\r\n        cancelled: { \r\n          class: 'danger', \r\n          text: 'Cancelada', \r\n          icon: 'fas fa-ban',\r\n          tooltip: 'Jornada cancelada'\r\n        },\r\n        postponed: { \r\n          class: 'secondary', \r\n          text: 'Postergada', \r\n          icon: 'fas fa-clock',\r\n          tooltip: 'Jornada postergada'\r\n        },\r\n        \r\n        // Estados de clubes\r\n        suspended: { \r\n          class: 'danger', \r\n          text: 'Suspendido', \r\n          icon: 'fas fa-ban',\r\n          tooltip: 'Club suspendido'\r\n        },\r\n        verified: { \r\n          class: 'success', \r\n          text: 'Verificado', \r\n          icon: 'fas fa-shield-alt',\r\n          tooltip: 'Club verificado'\r\n        },\r\n        unverified: { \r\n          class: 'warning', \r\n          text: 'No Verificado', \r\n          icon: 'fas fa-exclamation-triangle',\r\n          tooltip: 'Club pendiente de verificacin'\r\n        }\r\n      };\r\n      \r\n      return configs[this.status] || {\r\n        class: 'secondary',\r\n        text: this.status,\r\n        icon: 'fas fa-question',\r\n        tooltip: `Estado: ${this.status}`\r\n      };\r\n    },\r\n    badgeClasses() {\r\n      const baseClass = 'badge';\r\n      const sizeClass = this.size === 'sm' ? 'badge-sm' : this.size === 'lg' ? 'badge-lg' : '';\r\n      const colorClass = `badge-${this.statusConfig.class}`;\r\n      \r\n      return [baseClass, colorClass, sizeClass].filter(Boolean).join(' ');\r\n    },\r\n    displayText() {\r\n      return this.statusConfig.text;\r\n    },\r\n    iconClass() {\r\n      return this.statusConfig.icon;\r\n    },\r\n    tooltipText() {\r\n      return this.statusConfig.tooltip;\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.badge {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  gap: 0.25rem;\r\n  font-weight: 500;\r\n  white-space: nowrap;\r\n}\r\n\r\n.badge-sm {\r\n  font-size: 0.75rem;\r\n  padding: 0.25rem 0.5rem;\r\n}\r\n\r\n.badge-lg {\r\n  font-size: 1rem;\r\n  padding: 0.5rem 0.75rem;\r\n}\r\n\r\n.badge i {\r\n  font-size: 0.8em;\r\n}\r\n\r\n/* Bootstrap badge colors */\r\n.badge-primary {\r\n  color: #fff;\r\n  background-color: #007bff;\r\n}\r\n\r\n.badge-secondary {\r\n  color: #fff;\r\n  background-color: #6c757d;\r\n}\r\n\r\n.badge-success {\r\n  color: #fff;\r\n  background-color: #28a745;\r\n}\r\n\r\n.badge-danger {\r\n  color: #fff;\r\n  background-color: #dc3545;\r\n}\r\n\r\n.badge-warning {\r\n  color: #212529;\r\n  background-color: #ffc107;\r\n}\r\n\r\n.badge-info {\r\n  color: #fff;\r\n  background-color: #17a2b8;\r\n}\r\n\r\n.badge-light {\r\n  color: #212529;\r\n  background-color: #f8f9fa;\r\n}\r\n\r\n.badge-dark {\r\n  color: #fff;\r\n  background-color: #343a40;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
