___CSS_LOADER_EXPORT___.push([module.id, ".pagination-info[data-v-327051e0]{align-items:center;display:flex;height:32px}.page-link[data-v-327051e0]{border-color:#dee2e6;color:#6c757d}.page-link[data-v-327051e0]:hover{background-color:#e9ecef;border-color:#dee2e6;color:#495057}.page-item.active .page-link[data-v-327051e0]{background-color:#007bff;border-color:#007bff;color:#fff}.page-item.disabled .page-link[data-v-327051e0]{background-color:#fff;border-color:#dee2e6;color:#6c757d}@media (max-width:768px){.pagination-info[data-v-327051e0]{margin-bottom:1rem;text-align:center}.pagination[data-v-327051e0]{justify-content:center!important}}", "",{"version":3,"sources":["webpack://./resources/js/components/DataPagination.vue"],"names":[],"mappings":"AAyMA,kCAEA,kBAAA,CADA,YAAA,CAEA,WACA,CAEA,4BACA,oBAAA,CACA,aACA,CAEA,kCACA,wBAAA,CACA,oBAAA,CACA,aACA,CAEA,8CACA,wBAAA,CACA,oBAAA,CACA,UACA,CAEA,gDAEA,qBAAA,CACA,oBAAA,CAFA,aAGA,CAEA,yBACA,kCAEA,kBAAA,CADA,iBAEA,CAEA,6BACA,gCACA,CACA","sourcesContent":["<template>\r\n  <div class=\"data-pagination\" v-if=\"shouldShowPagination\">\r\n    <div class=\"row align-items-center\">\r\n      <!-- Informacin de pgina -->\r\n      <div class=\"col-md-6\">\r\n        <div class=\"pagination-info\">\r\n          <small class=\"text-muted\">\r\n            Mostrando {{ startItem }} al {{ endItem }} de {{ total }} registros\r\n          </small>\r\n        </div>\r\n      </div>\r\n\r\n      <!-- Controles de paginacin -->\r\n      <div class=\"col-md-6\">\r\n        <nav aria-label=\"Navegacin de pginas\">\r\n          <ul class=\"pagination pagination-sm justify-content-end mb-0\">\r\n            <!-- Botn Anterior -->\r\n            <li class=\"page-item\" :class=\"{ disabled: currentPage === 1 }\">\r\n              <button \r\n                class=\"page-link\" \r\n                @click=\"goToPage(currentPage - 1)\"\r\n                :disabled=\"currentPage === 1\"\r\n              >\r\n                <i class=\"fas fa-chevron-left\"></i>\r\n                Anterior\r\n              </button>\r\n            </li>\r\n\r\n            <!-- Primera pgina -->\r\n            <li class=\"page-item\" v-if=\"showFirstPage\">\r\n              <button class=\"page-link\" @click=\"goToPage(1)\">1</button>\r\n            </li>\r\n            \r\n            <!-- Separador inicial -->\r\n            <li class=\"page-item disabled\" v-if=\"showFirstSeparator\">\r\n              <span class=\"page-link\">...</span>\r\n            </li>\r\n\r\n            <!-- Pginas visibles -->\r\n            <li \r\n              class=\"page-item\" \r\n              v-for=\"page in visiblePages\" \r\n              :key=\"page\"\r\n              :class=\"{ active: page === currentPage }\"\r\n            >\r\n              <button class=\"page-link\" @click=\"goToPage(page)\">\r\n                {{ page }}\r\n              </button>\r\n            </li>\r\n\r\n            <!-- Separador final -->\r\n            <li class=\"page-item disabled\" v-if=\"showLastSeparator\">\r\n              <span class=\"page-link\">...</span>\r\n            </li>\r\n\r\n            <!-- ltima pgina -->\r\n            <li class=\"page-item\" v-if=\"showLastPage\">\r\n              <button class=\"page-link\" @click=\"goToPage(lastPage)\">\r\n                {{ lastPage }}\r\n              </button>\r\n            </li>\r\n\r\n            <!-- Botn Siguiente -->\r\n            <li class=\"page-item\" :class=\"{ disabled: currentPage === lastPage }\">\r\n              <button \r\n                class=\"page-link\" \r\n                @click=\"goToPage(currentPage + 1)\"\r\n                :disabled=\"currentPage === lastPage\"\r\n              >\r\n                Siguiente\r\n                <i class=\"fas fa-chevron-right\"></i>\r\n              </button>\r\n            </li>\r\n          </ul>\r\n        </nav>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Selector de items por pgina -->\r\n    <div class=\"row mt-2\" v-if=\"showPerPageSelector\">\r\n      <div class=\"col-12\">\r\n        <div class=\"d-flex align-items-center justify-content-center\">\r\n          <label class=\"mb-0 mr-2\">\r\n            <small>Mostrar:</small>\r\n          </label>\r\n          <select \r\n            class=\"form-control form-control-sm d-inline-block w-auto\"\r\n            v-model=\"itemsPerPage\"\r\n            @change=\"onPerPageChange\"\r\n          >\r\n            <option v-for=\"option in perPageOptions\" :key=\"option\" :value=\"option\">\r\n              {{ option }}\r\n            </option>\r\n          </select>\r\n          <span class=\"ml-2\">\r\n            <small>por pgina</small>\r\n          </span>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'DataPagination',\r\n  props: {\r\n    currentPage: {\r\n      type: Number,\r\n      required: true\r\n    },\r\n    total: {\r\n      type: Number,\r\n      required: true\r\n    },\r\n    perPage: {\r\n      type: Number,\r\n      default: 15\r\n    },\r\n    maxVisiblePages: {\r\n      type: Number,\r\n      default: 5\r\n    },\r\n    showPerPageSelector: {\r\n      type: Boolean,\r\n      default: true\r\n    },\r\n    perPageOptions: {\r\n      type: Array,\r\n      default: () => [10, 15, 25, 50, 100]\r\n    }\r\n  },\r\n  data() {\r\n    return {\r\n      itemsPerPage: this.perPage\r\n    };\r\n  },\r\n  computed: {\r\n    lastPage() {\r\n      return Math.ceil(this.total / this.itemsPerPage);\r\n    },\r\n    startItem() {\r\n      return (this.currentPage - 1) * this.itemsPerPage + 1;\r\n    },\r\n    endItem() {\r\n      const end = this.currentPage * this.itemsPerPage;\r\n      return end > this.total ? this.total : end;\r\n    },\r\n    shouldShowPagination() {\r\n      return this.total > this.itemsPerPage;\r\n    },\r\n    visiblePages() {\r\n      const pages = [];\r\n      const half = Math.floor(this.maxVisiblePages / 2);\r\n      \r\n      let start = Math.max(1, this.currentPage - half);\r\n      let end = Math.min(this.lastPage, start + this.maxVisiblePages - 1);\r\n      \r\n      // Ajustar el inicio si estamos cerca del final\r\n      if (end - start + 1 < this.maxVisiblePages) {\r\n        start = Math.max(1, end - this.maxVisiblePages + 1);\r\n      }\r\n      \r\n      for (let i = start; i <= end; i++) {\r\n        pages.push(i);\r\n      }\r\n      \r\n      return pages;\r\n    },\r\n    showFirstPage() {\r\n      return this.visiblePages[0] > 1;\r\n    },\r\n    showLastPage() {\r\n      return this.visiblePages[this.visiblePages.length - 1] < this.lastPage;\r\n    },\r\n    showFirstSeparator() {\r\n      return this.visiblePages[0] > 2;\r\n    },\r\n    showLastSeparator() {\r\n      return this.visiblePages[this.visiblePages.length - 1] < this.lastPage - 1;\r\n    }\r\n  },\r\n  watch: {\r\n    perPage(newVal) {\r\n      this.itemsPerPage = newVal;\r\n    }\r\n  },\r\n  methods: {\r\n    goToPage(page) {\r\n      if (page >= 1 && page <= this.lastPage && page !== this.currentPage) {\r\n        this.$emit('page-change', page);\r\n      }\r\n    },\r\n    onPerPageChange() {\r\n      this.$emit('per-page-change', this.itemsPerPage);\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.pagination-info {\r\n  display: flex;\r\n  align-items: center;\r\n  height: 32px; /* Align with pagination buttons */\r\n}\r\n\r\n.page-link {\r\n  border-color: #dee2e6;\r\n  color: #6c757d;\r\n}\r\n\r\n.page-link:hover {\r\n  background-color: #e9ecef;\r\n  border-color: #dee2e6;\r\n  color: #495057;\r\n}\r\n\r\n.page-item.active .page-link {\r\n  background-color: #007bff;\r\n  border-color: #007bff;\r\n  color: white;\r\n}\r\n\r\n.page-item.disabled .page-link {\r\n  color: #6c757d;\r\n  background-color: #fff;\r\n  border-color: #dee2e6;\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .pagination-info {\r\n    text-align: center;\r\n    margin-bottom: 1rem;\r\n  }\r\n  \r\n  .pagination {\r\n    justify-content: center !important;\r\n  }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
