import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".categories-table[data-v-6af50570]{font-size:.9rem}.championship-column[data-v-6af50570]{max-width:120px;min-width:120px}.pilot-count-cell[data-v-6af50570]{vertical-align:middle}.zero-count[data-v-6af50570]{color:#6c757d;font-weight:400}.badge-pink[data-v-6af50570]{background-color:#e83e8c;color:#fff}.small-box[data-v-6af50570]{border-radius:.375rem;box-shadow:0 1px 1px rgba(0,0,0,.1);display:block;margin-bottom:20px;position:relative}.small-box>.inner[data-v-6af50570]{padding:10px}.small-box .icon[data-v-6af50570]{color:rgba(0,0,0,.15);font-size:90px;position:absolute;right:10px;top:-10px;transition:all .3s linear;z-index:0}.small-box h3[data-v-6af50570]{font-size:2.2rem;font-weight:700;margin:0 0 10px;padding:0;white-space:nowrap}.small-box p[data-v-6af50570]{font-size:1rem}.small-box h3[data-v-6af50570],.small-box p[data-v-6af50570]{position:relative;z-index:5}.bg-info[data-v-6af50570]{background-color:#17a2b8!important;color:#fff}.bg-success[data-v-6af50570]{background-color:#28a745!important;color:#fff}.bg-warning[data-v-6af50570]{background-color:#ffc107!important;color:#212529}.bg-danger[data-v-6af50570]{background-color:#dc3545!important;color:#fff}.table-active[data-v-6af50570]{background-color:rgba(0,0,0,.075)}@media (max-width:768px){.championship-column[data-v-6af50570]{max-width:100px;min-width:100px}.small-box h3[data-v-6af50570]{font-size:1.8rem}.small-box .icon[data-v-6af50570]{font-size:60px}}", "",{"version":3,"sources":["webpack://./resources/js/components/CategoryManager.vue"],"names":[],"mappings":"AA8lBA,mCACA,eACA,CAEA,sCAEA,eAAA,CADA,eAEA,CAEA,mCACA,qBACA,CAEA,6BACA,aAAA,CACA,eACA,CAEA,6BAEA,wBAAA,CADA,UAEA,CAEA,4BACA,qBAAA,CAIA,mCAAA,CAFA,aAAA,CACA,kBAAA,CAFA,iBAIA,CAEA,mCACA,YACA,CAEA,kCAOA,qBAAA,CADA,cAAA,CAJA,iBAAA,CAEA,UAAA,CADA,SAAA,CAFA,yBAAA,CAIA,SAGA,CAEA,+BACA,gBAAA,CACA,eAAA,CACA,eAAA,CAEA,SAAA,CADA,kBAEA,CAEA,8BACA,cACA,CAEA,6DAEA,iBAAA,CADA,SAEA,CAEA,0BACA,kCAAA,CACA,UACA,CAEA,6BACA,kCAAA,CACA,UACA,CAEA,6BACA,kCAAA,CACA,aACA,CAEA,4BACA,kCAAA,CACA,UACA,CAEA,+BACA,iCACA,CAEA,yBACA,sCAEA,eAAA,CADA,eAEA,CAEA,+BACA,gBACA,CAEA,kCACA,cACA,CACA","sourcesContent":["<template>\r\n    <div class=\"category-manager\">\r\n        <!-- Estadsticas -->\r\n        <div class=\"row mb-4\">\r\n            <div class=\"col-lg-3 col-6\">\r\n                <div class=\"small-box bg-info\">\r\n                    <div class=\"inner\">\r\n                        <h3>{{ stats.total }}</h3>\r\n                        <p>Total Categoras</p>\r\n                    </div>\r\n                    <div class=\"icon\">\r\n                        <i class=\"fas fa-tags\"></i>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-lg-3 col-6\">\r\n                <div class=\"small-box bg-success\">\r\n                    <div class=\"inner\">\r\n                        <h3>{{ stats.active }}</h3>\r\n                        <p>Categoras Activas</p>\r\n                    </div>\r\n                    <div class=\"icon\">\r\n                        <i class=\"fas fa-check-circle\"></i>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-lg-3 col-6\">\r\n                <div class=\"small-box bg-warning\">\r\n                    <div class=\"inner\">\r\n                        <h3>{{ stats.championships }}</h3>\r\n                        <p>Campeonatos Activos</p>\r\n                    </div>\r\n                    <div class=\"icon\">\r\n                        <i class=\"fas fa-trophy\"></i>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div class=\"col-lg-3 col-6\">\r\n                <div class=\"small-box bg-danger\">\r\n                    <div class=\"inner\">\r\n                        <h3>{{ stats.totalPilots }}</h3>\r\n                        <p>Total Pilotos</p>\r\n                    </div>\r\n                    <div class=\"icon\">\r\n                        <i class=\"fas fa-users\"></i>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Card principal -->\r\n        <div class=\"card\">\r\n            <div class=\"card-header\">\r\n                <h3 class=\"card-title\">Listado de Categoras</h3>\r\n                <div class=\"card-tools\">\r\n                    <div class=\"btn-group\">\r\n                        <button @click=\"exportCategories\" class=\"btn btn-success btn-sm\" title=\"Exportar a CSV\">\r\n                            <i class=\"fas fa-download\"></i> Exportar\r\n                        </button>\r\n                        <router-link v-if=\"canCreate\" :to=\"{ name: 'categories.create' }\" class=\"btn btn-primary btn-sm\">\r\n                            <i class=\"fas fa-plus\"></i> Nueva Categora\r\n                        </router-link>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n\r\n            <div class=\"card-body\">\r\n                <!-- Loading Spinner -->\r\n                <LoadingSpinner v-if=\"loading\" />\r\n\r\n                <template v-else>\r\n                    <!-- Filtros -->\r\n                    <form @submit.prevent=\"loadCategories\" class=\"mb-3\">\r\n                        <div class=\"row\">\r\n                            <div class=\"col-md-3\">\r\n                                <input \r\n                                    type=\"text\" \r\n                                    v-model=\"filters.search\"\r\n                                    @input=\"handleSearchInput\"\r\n                                    class=\"form-control\" \r\n                                    placeholder=\"Buscar categoras...\">\r\n                            </div>\r\n                            <div class=\"col-md-2\">\r\n                                <select v-model=\"filters.type\" @change=\"handleFilterChange\" class=\"form-control\">\r\n                                    <option value=\"\">Todos los tipos</option>\r\n                                    <option value=\"escuela\">Escuela</option>\r\n                                    <option value=\"novicios\">Novicios</option>\r\n                                    <option value=\"juvenil\">Juvenil</option>\r\n                                    <option value=\"adulto\">Adulto</option>\r\n                                </select>\r\n                            </div>\r\n                            <div class=\"col-md-2\">\r\n                                <select v-model=\"filters.gender\" @change=\"handleFilterChange\" class=\"form-control\">\r\n                                    <option value=\"\">Todos los gneros</option>\r\n                                    <option value=\"varones\">Varones</option>\r\n                                    <option value=\"mujeres\">Mujeres</option>\r\n                                </select>\r\n                            </div>\r\n                            <div class=\"col-md-2\">\r\n                                <select v-model=\"filters.status\" @change=\"handleFilterChange\" class=\"form-control\">\r\n                                    <option value=\"\">Todos los estados</option>\r\n                                    <option value=\"active\">Activa</option>\r\n                                    <option value=\"inactive\">Inactiva</option>\r\n                                </select>\r\n                            </div>\r\n                            <div class=\"col-md-3\">\r\n                                <button type=\"submit\" class=\"btn btn-info\">\r\n                                    <i class=\"fas fa-search\"></i> Buscar\r\n                                </button>\r\n                                <button type=\"button\" @click=\"clearFilters\" class=\"btn btn-secondary\">\r\n                                    <i class=\"fas fa-times\"></i> Limpiar\r\n                                </button>\r\n                            </div>\r\n                        </div>\r\n                    </form>\r\n\r\n                    <!-- Nota explicativa -->\r\n                    <div class=\"alert alert-info mb-3\">\r\n                        <i class=\"fas fa-info-circle mr-2\"></i>\r\n                        <strong>Diferencias entre conteos:</strong>\r\n                        <br>\r\n                         <strong>Columnas de campeonatos:</strong> Nmero de pilotos <em>registrados</em> en ese campeonato especfico para esta categora\r\n                        <br>\r\n                         <strong>Pilotos Asignados:</strong> Total de pilotos que tienen asignada esta categora en su perfil general\r\n                        <br>\r\n                         <strong>Por qu son diferentes?</strong> Un piloto puede tener una categora asignada en su perfil pero no estar inscrito en ningn campeonato, o puede participar en campeonatos con una categora diferente a la de su perfil\r\n                    </div>\r\n\r\n                    <!-- Tabla de categoras -->\r\n                    <div class=\"table-responsive\">\r\n                        <table class=\"table table-bordered table-hover categories-table\">\r\n                            <thead class=\"thead-light\">\r\n                                <tr>\r\n                                    <th style=\"min-width: 200px;\">Nombre</th>\r\n                                    <th style=\"min-width: 100px;\">Tipo</th>\r\n                                    <th style=\"min-width: 100px;\">Gnero</th>\r\n                                    <th style=\"min-width: 130px;\">Rango de Edad</th>\r\n                                    <th \r\n                                        v-for=\"championship in championships\" \r\n                                        :key=\"championship.id\"\r\n                                        class=\"text-center championship-column\"\r\n                                        :title=\"`Pilotos registrados especficamente en ${championship.name} (${championship.year}) para esta categora`\"\r\n                                    >\r\n                                        <div class=\"font-weight-bold\">{{ truncateText(championship.name, 15) }}</div>\r\n                                        <small class=\"text-muted d-block\">{{ championship.year }}</small>\r\n                                        <small class=\"text-info d-block\">Registrados</small>\r\n                                    </th>\r\n                                    <th class=\"text-center\" style=\"min-width: 80px;\" title=\"Total de pilotos que tienen asignada esta categora en su perfil (independiente de inscripciones a campeonatos)\">\r\n                                        <div class=\"font-weight-bold\">Pilotos</div>\r\n                                        <small class=\"text-muted d-block\">Asignados</small>\r\n                                    </th>\r\n                                    <th class=\"text-center\" style=\"min-width: 100px;\">Estado</th>\r\n                                    <th class=\"text-center\" style=\"min-width: 120px;\">Acciones</th>\r\n                                </tr>\r\n                            </thead>\r\n                            <tbody>\r\n                                <tr v-for=\"category in categories\" :key=\"category.id\">\r\n                                    <td>\r\n                                        <strong>{{ category.name }}</strong>\r\n                                        <br v-if=\"category.description\">\r\n                                        <small v-if=\"category.description\" class=\"text-muted\">\r\n                                            {{ truncateText(category.description, 50) }}\r\n                                        </small>\r\n                                    </td>\r\n                                    <td>\r\n                                        <span :class=\"getTypeBadgeClass(category.type)\">\r\n                                            {{ capitalizeFirst(category.type) }}\r\n                                        </span>\r\n                                    </td>\r\n                                    <td>\r\n                                        <span v-if=\"category.gender\" :class=\"getGenderBadgeClass(category.gender)\">\r\n                                            {{ capitalizeFirst(category.gender) }}\r\n                                        </span>\r\n                                        <span v-else class=\"text-muted\">Mixto</span>\r\n                                    </td>\r\n                                    <td>{{ formatAgeRange(category) }}</td>\r\n                                    <td \r\n                                        v-for=\"championship in championships\" \r\n                                        :key=\"championship.id\"\r\n                                        class=\"text-center pilot-count-cell\"\r\n                                        :title=\"`${getChampionshipCount(category, championship.id)} pilotos registrados en ${championship.name} ${championship.year} para la categora '${category.name}'`\"\r\n                                    >\r\n                                        <span \r\n                                            v-if=\"getChampionshipCount(category, championship.id) > 0\" \r\n                                            class=\"badge badge-success\"\r\n                                        >\r\n                                            {{ getChampionshipCount(category, championship.id) }}\r\n                                        </span>\r\n                                        <span v-else class=\"zero-count\">-</span>\r\n                                    </td>\r\n                                    <td class=\"text-center pilot-count-cell\" :title=\"`${category.pilots_count || 0} pilotos tienen asignada la categora '${category.name}' en su perfil`\">\r\n                                        <span class=\"badge badge-info\">{{ category.pilots_count || 0 }}</span>\r\n                                    </td>\r\n                                    <td class=\"text-center\">\r\n                                        <button \r\n                                            v-if=\"canEdit\" \r\n                                            @click=\"toggleStatus(category)\"\r\n                                            :class=\"getStatusButtonClass(category.active)\"\r\n                                            :title=\"category.active ? 'Desactivar' : 'Activar'\"\r\n                                        >\r\n                                            <i :class=\"category.active ? 'fas fa-check' : 'fas fa-times'\"></i>\r\n                                            {{ category.active ? 'Activa' : 'Inactiva' }}\r\n                                        </button>\r\n                                        <span v-else :class=\"getStatusBadgeClass(category.active)\">\r\n                                            <i :class=\"category.active ? 'fas fa-check' : 'fas fa-times'\"></i>\r\n                                            {{ category.active ? 'Activa' : 'Inactiva' }}\r\n                                        </span>\r\n                                    </td>\r\n                                    <td class=\"text-center\">\r\n                                        <div class=\"btn-group btn-group-sm\" role=\"group\">\r\n                                            <router-link :to=\"{ name: 'categories.show', params: { id: category.id } }\" class=\"btn btn-info btn-sm\" title=\"Ver detalles\">\r\n                                                <i class=\"fas fa-eye\"></i>\r\n                                            </router-link>\r\n                                            <router-link v-if=\"canEdit\" :to=\"{ name: 'categories.edit', params: { id: category.id } }\" class=\"btn btn-warning btn-sm\" title=\"Editar\">\r\n                                                <i class=\"fas fa-edit\"></i>\r\n                                            </router-link>\r\n                                            <button \r\n                                                v-if=\"canDelete\" \r\n                                                @click=\"confirmDelete(category)\"\r\n                                                class=\"btn btn-danger btn-sm\" \r\n                                                title=\"Eliminar\"\r\n                                            >\r\n                                                <i class=\"fas fa-trash\"></i>\r\n                                            </button>\r\n                                        </div>\r\n                                    </td>\r\n                                </tr>\r\n                                <tr v-if=\"categories.length === 0\">\r\n                                    <td :colspan=\"7 + championships.length\" class=\"text-center text-muted\">\r\n                                        <i class=\"fas fa-info-circle\"></i> No hay categoras registradas\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </table>\r\n                    </div>\r\n\r\n                    <!-- Paginacin -->\r\n                    <div v-if=\"pagination.total > 0\" class=\"mt-3\">\r\n                        <DataPagination\r\n                            :current-page=\"pagination.current_page\"\r\n                            :last-page=\"pagination.last_page\"\r\n                            :total=\"pagination.total\"\r\n                            :per-page=\"pagination.per_page\"\r\n                            :from=\"pagination.from\"\r\n                            :to=\"pagination.to\"\r\n                            @page-change=\"changePage\"\r\n                        />\r\n                    </div>\r\n                </template>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport LoadingSpinner from './LoadingSpinner.vue';\r\nimport DataPagination from './DataPagination.vue';\r\n\r\nexport default {\r\n    name: 'CategoryManager',\r\n    components: {\r\n        LoadingSpinner,\r\n        DataPagination\r\n    },\r\n    data() {\r\n        return {\r\n            categories: [],\r\n            championships: [],\r\n            loading: true,\r\n            searchTimeout: null,\r\n            filters: {\r\n                search: '',\r\n                type: '',\r\n                gender: '',\r\n                status: ''\r\n            },\r\n            pagination: {\r\n                current_page: 1,\r\n                last_page: 1,\r\n                total: 0,\r\n                per_page: 15,\r\n                from: 0,\r\n                to: 0\r\n            },\r\n            stats: {\r\n                total: 0,\r\n                active: 0,\r\n                championships: 0,\r\n                totalPilots: 0\r\n            },\r\n            routes: {\r\n                api: '/api/categories',\r\n                show: '/categorias/{id}',\r\n                edit: '/gestionar/categorias/{id}/editar',\r\n                create: '/gestionar/categorias/crear',\r\n                delete: '/api/categories/{id}',\r\n                toggleStatus: '/gestionar/categorias/{id}/cambiar-estado',\r\n                export: '/api/categories/export'\r\n            },\r\n            permissions: {\r\n                canCreate: window.Laravel?.user?.authenticated || false,\r\n                canEdit: window.Laravel?.user?.authenticated || false,\r\n                canDelete: window.Laravel?.user?.authenticated || false\r\n            }\r\n        }\r\n    },\r\n    computed: {\r\n        canCreate() {\r\n            return this.permissions.canCreate;\r\n        },\r\n        canEdit() {\r\n            return this.permissions.canEdit;\r\n        },\r\n        canDelete() {\r\n            return this.permissions.canDelete;\r\n        }\r\n    },\r\n    mounted() {\r\n        console.log('CategoryManager mounted, checking for initial data...');\r\n        console.log('window.Laravel:', window.Laravel);\r\n        console.log('window.Laravel.initialData:', window.Laravel?.initialData);\r\n        \r\n        // Check if we have initial data from server\r\n        if (window.Laravel && window.Laravel.initialData && window.Laravel.initialData.page === 'categories-list') {\r\n            console.log('Using initial categories data from server:', window.Laravel.initialData);\r\n            const data = window.Laravel.initialData.categories;\r\n            \r\n            if (data.data) {\r\n                this.categories = data.data;\r\n                this.pagination = {\r\n                    current_page: data.current_page || 1,\r\n                    last_page: data.last_page || 1,\r\n                    total: data.total || 0,\r\n                    per_page: data.per_page || 10,\r\n                    from: data.from || 0,\r\n                    to: data.to || 0\r\n                };\r\n            } else {\r\n                this.categories = Array.isArray(data) ? data : [];\r\n                this.pagination.total = this.categories.length;\r\n            }\r\n            \r\n            // When using initial data, we need to load additional data from API\r\n            this.loadAdditionalData();\r\n            this.loading = false;\r\n        } else {\r\n            console.log('No initial data found, loading from API...');\r\n            this.loadCategories();\r\n        }\r\n    },\r\n    methods: {\r\n        async loadAdditionalData() {\r\n            try {\r\n                console.log('CategoryManager: loadAdditionalData called, API URL:', this.routes.api);\r\n                const response = await axios.get(this.routes.api);\r\n                \r\n                console.log('CategoryManager: Additional data response:', response.data);\r\n                const data = response.data;\r\n                this.championships = data.championships || [];\r\n                this.stats = data.stats || this.calculateLocalStats();\r\n            } catch (error) {\r\n                console.error('Error loading additional data:', error);\r\n                // Fallback to local stats calculation\r\n                this.stats = this.calculateLocalStats();\r\n            }\r\n        },\r\n\r\n        calculateLocalStats() {\r\n            return {\r\n                total: this.categories.length,\r\n                active: this.categories.filter(cat => cat.active).length,\r\n                championships: 0,\r\n                totalPilots: this.categories.reduce((sum, cat) => sum + (cat.pilots_count || 0), 0)\r\n            };\r\n        },\r\n\r\n        async loadCategories() {\r\n            this.loading = true;\r\n            console.log('CategoryManager: loadCategories called, loading from API...');\r\n            console.log('CategoryManager: API URL:', this.routes.api);\r\n            try {\r\n                // Solo incluir parmetros que tienen valores\r\n                const params = new URLSearchParams();\r\n                params.append('page', this.pagination.current_page);\r\n                \r\n                if (this.filters.search && this.filters.search.trim() !== '') {\r\n                    params.append('search', this.filters.search.trim());\r\n                }\r\n                if (this.filters.type && this.filters.type !== '') {\r\n                    params.append('type', this.filters.type);\r\n                }\r\n                if (this.filters.gender && this.filters.gender !== '') {\r\n                    params.append('gender', this.filters.gender);\r\n                }\r\n                if (this.filters.status && this.filters.status !== '') {\r\n                    params.append('status', this.filters.status);\r\n                }\r\n\r\n                const apiUrl = `${this.routes.api}?${params}`;\r\n                console.log('CategoryManager: Fetching from URL:', apiUrl);\r\n                const response = await axios.get(apiUrl);\r\n                \r\n                console.log('CategoryManager: Response status:', response.status);\r\n                console.log('CategoryManager: Response data:', response.data);\r\n\r\n                const data = response.data;\r\n                this.categories = data.data || [];\r\n                this.championships = data.championships || [];\r\n                this.stats = data.stats || this.calculateLocalStats();\r\n                this.pagination = {\r\n                    current_page: data.current_page || 1,\r\n                    last_page: data.last_page || 1,\r\n                    total: data.total || 0,\r\n                    per_page: data.per_page || 15,\r\n                    from: data.from || 0,\r\n                    to: data.to || 0\r\n                };\r\n                \r\n                console.log('CategoryManager: Categories loaded:', this.categories.length);\r\n            } catch (error) {\r\n                console.error('Error loading categories:', error);\r\n                this.showNotification('Error al cargar las categoras', 'error');\r\n                this.categories = [];\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        handleSearchInput() {\r\n            // Auto-submit con delay\r\n            if (this.searchTimeout) {\r\n                clearTimeout(this.searchTimeout);\r\n            }\r\n            this.searchTimeout = setTimeout(() => {\r\n                this.pagination.current_page = 1;\r\n                this.loadCategories();\r\n            }, 800);\r\n        },\r\n\r\n        handleFilterChange() {\r\n            this.pagination.current_page = 1;\r\n            this.loadCategories();\r\n        },\r\n\r\n        clearFilters() {\r\n            this.filters.search = '';\r\n            this.filters.type = '';\r\n            this.filters.gender = '';\r\n            this.filters.status = '';\r\n            this.pagination.current_page = 1;\r\n            this.loadCategories();\r\n        },\r\n\r\n        changePage(page) {\r\n            this.pagination.current_page = page;\r\n            this.loadCategories();\r\n        },\r\n\r\n        async toggleStatus(category) {\r\n            try {\r\n                const response = await fetch(this.routes.toggleStatus.replace('{id}', category.id), {\r\n                    method: 'POST',\r\n                    headers: {\r\n                        'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content'),\r\n                        'Content-Type': 'application/json',\r\n                        'Accept': 'application/json'\r\n                    }\r\n                });\r\n\r\n                if (!response.ok) {\r\n                    throw new Error('Error al cambiar el estado');\r\n                }\r\n\r\n                const status = category.active ? 'desactivada' : 'activada';\r\n                category.active = !category.active;\r\n                this.showNotification(`Categora ${status} exitosamente`, 'success');\r\n                \r\n                // Actualizar estadsticas\r\n                this.updateStats();\r\n            } catch (error) {\r\n                console.error('Error toggling status:', error);\r\n                this.showNotification('Error al cambiar el estado de la categora', 'error');\r\n            }\r\n        },\r\n\r\n        async confirmDelete(category) {\r\n            if (confirm(`Est seguro de eliminar la categora \"${category.name}\"? Esta accin no se puede deshacer.`)) {\r\n                await this.deleteCategory(category);\r\n            }\r\n        },\r\n\r\n        async deleteCategory(category) {\r\n            try {\r\n                const response = await fetch(this.routes.delete.replace('{id}', category.id), {\r\n                    method: 'DELETE',\r\n                    headers: {\r\n                        'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content'),\r\n                        'Content-Type': 'application/json',\r\n                        'Accept': 'application/json'\r\n                    }\r\n                });\r\n\r\n                if (!response.ok) {\r\n                    const errorData = await response.json();\r\n                    throw new Error(errorData.message || 'Error al eliminar la categora');\r\n                }\r\n\r\n                this.showNotification('Categora eliminada exitosamente', 'success');\r\n                this.loadCategories();\r\n            } catch (error) {\r\n                console.error('Error deleting category:', error);\r\n                this.showNotification(error.message || 'Error al eliminar la categora', 'error');\r\n            }\r\n        },\r\n\r\n        async exportCategories() {\r\n            try {\r\n                const params = new URLSearchParams({\r\n                    search: this.filters.search,\r\n                    type: this.filters.type,\r\n                    gender: this.filters.gender,\r\n                    status: this.filters.status\r\n                });\r\n\r\n                const response = await fetch(`${this.routes.export}?${params}`);\r\n                \r\n                if (!response.ok) {\r\n                    throw new Error('Error al exportar las categoras');\r\n                }\r\n\r\n                const blob = await response.blob();\r\n                const url = window.URL.createObjectURL(blob);\r\n                const a = document.createElement('a');\r\n                a.href = url;\r\n                a.download = `categorias_bmx_${new Date().toISOString().split('T')[0]}.csv`;\r\n                document.body.appendChild(a);\r\n                a.click();\r\n                window.URL.revokeObjectURL(url);\r\n                document.body.removeChild(a);\r\n\r\n                this.showNotification('Categoras exportadas exitosamente', 'success');\r\n            } catch (error) {\r\n                console.error('Error exporting categories:', error);\r\n                this.showNotification('Error al exportar las categoras', 'error');\r\n            }\r\n        },\r\n\r\n        getTypeBadgeClass(type) {\r\n            const baseClass = 'badge';\r\n            switch (type) {\r\n                case 'novicios': return `${baseClass} badge-info`;\r\n                case 'escuela': return `${baseClass} badge-primary`;\r\n                default: return `${baseClass} badge-success`;\r\n            }\r\n        },\r\n\r\n        getGenderBadgeClass(gender) {\r\n            const baseClass = 'badge';\r\n            return gender === 'varones' ? `${baseClass} badge-primary` : `${baseClass} badge-pink`;\r\n        },\r\n\r\n        getStatusButtonClass(active) {\r\n            return `btn btn-sm ${active ? 'btn-success' : 'btn-secondary'}`;\r\n        },\r\n\r\n        getStatusBadgeClass(active) {\r\n            return `badge ${active ? 'badge-success' : 'badge-secondary'}`;\r\n        },\r\n\r\n        formatAgeRange(category) {\r\n            if (category.age_min && category.age_max) {\r\n                return `${category.age_min}-${category.age_max} aos`;\r\n            } else if (category.age_min) {\r\n                return `${category.age_min}+ aos`;\r\n            } else if (category.age_max) {\r\n                return `hasta ${category.age_max} aos`;\r\n            }\r\n            return 'Sin lmite';\r\n        },\r\n\r\n        getChampionshipCount(category, championshipId) {\r\n            return category.championship_counts?.[championshipId] || 0;\r\n        },\r\n\r\n        updateStats() {\r\n            this.stats = this.calculateLocalStats();\r\n        },\r\n\r\n        capitalizeFirst(str) {\r\n            return str.charAt(0).toUpperCase() + str.slice(1);\r\n        },\r\n\r\n        truncateText(text, length) {\r\n            if (!text) return '';\r\n            return text.length > length ? text.substring(0, length) + '...' : text;\r\n        },\r\n\r\n        showNotification(message, type) {\r\n            // Emit event for notification system\r\n            this.$root.$emit('show-notification', { message, type });\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.categories-table {\r\n    font-size: 0.9rem;\r\n}\r\n\r\n.championship-column {\r\n    min-width: 120px;\r\n    max-width: 120px;\r\n}\r\n\r\n.pilot-count-cell {\r\n    vertical-align: middle;\r\n}\r\n\r\n.zero-count {\r\n    color: #6c757d;\r\n    font-weight: normal;\r\n}\r\n\r\n.badge-pink {\r\n    color: #fff;\r\n    background-color: #e83e8c;\r\n}\r\n\r\n.small-box {\r\n    border-radius: 0.375rem;\r\n    position: relative;\r\n    display: block;\r\n    margin-bottom: 20px;\r\n    box-shadow: 0 1px 1px rgba(0,0,0,0.1);\r\n}\r\n\r\n.small-box > .inner {\r\n    padding: 10px;\r\n}\r\n\r\n.small-box .icon {\r\n    transition: all 0.3s linear;\r\n    position: absolute;\r\n    top: -10px;\r\n    right: 10px;\r\n    z-index: 0;\r\n    font-size: 90px;\r\n    color: rgba(0,0,0,0.15);\r\n}\r\n\r\n.small-box h3 {\r\n    font-size: 2.2rem;\r\n    font-weight: bold;\r\n    margin: 0 0 10px 0;\r\n    white-space: nowrap;\r\n    padding: 0;\r\n}\r\n\r\n.small-box p {\r\n    font-size: 1rem;\r\n}\r\n\r\n.small-box h3, .small-box p {\r\n    z-index: 5;\r\n    position: relative;\r\n}\r\n\r\n.bg-info {\r\n    background-color: #17a2b8 !important;\r\n    color: #fff;\r\n}\r\n\r\n.bg-success {\r\n    background-color: #28a745 !important;\r\n    color: #fff;\r\n}\r\n\r\n.bg-warning {\r\n    background-color: #ffc107 !important;\r\n    color: #212529;\r\n}\r\n\r\n.bg-danger {\r\n    background-color: #dc3545 !important;\r\n    color: #fff;\r\n}\r\n\r\n.table-active {\r\n    background-color: rgba(0,0,0,0.075);\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    .championship-column {\r\n        min-width: 100px;\r\n        max-width: 100px;\r\n    }\r\n    \r\n    .small-box h3 {\r\n        font-size: 1.8rem;\r\n    }\r\n    \r\n    .small-box .icon {\r\n        font-size: 60px;\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
