import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/cssWithMappingToString.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".search-filter-container[data-v-93a13984]{background:#f8f9fa;border-radius:.5rem;margin-bottom:1rem;padding:1rem}.input-group-text[data-v-93a13984]{background-color:#e9ecef;border-color:#ced4da}", "",{"version":3,"sources":["webpack://./resources/js/components/SearchFilter.vue"],"names":[],"mappings":"AA4KA,0CACA,kBAAA,CAEA,mBAAA,CACA,kBAAA,CAFA,YAGA,CAEA,mCACA,wBAAA,CACA,oBACA","sourcesContent":["<template>\r\n  <div class=\"search-filter-container\">\r\n    <!-- Bsqueda principal -->\r\n    <div class=\"row mb-3\">\r\n      <div class=\"col-md-6\">\r\n        <div class=\"input-group\">\r\n          <input \r\n            type=\"text\" \r\n            class=\"form-control\" \r\n            :placeholder=\"searchPlaceholder\"\r\n            v-model=\"searchQuery\"\r\n            @input=\"onSearchInput\"\r\n          />\r\n          <div class=\"input-group-append\">\r\n            <span class=\"input-group-text\">\r\n              <i class=\"fas fa-search\"></i>\r\n            </span>\r\n          </div>\r\n        </div>\r\n      </div>\r\n      \r\n      <!-- Filtros adicionales -->\r\n      <div class=\"col-md-6\" v-if=\"hasFilters\">\r\n        <div class=\"row\">\r\n          <div class=\"col-md-6\" v-for=\"filter in filters\" :key=\"filter.key\">\r\n            <select \r\n              class=\"form-control\"\r\n              v-model=\"filterValues[filter.key]\"\r\n              @change=\"onFilterChange\"\r\n            >\r\n              <option value=\"\">{{ filter.placeholder }}</option>\r\n              <option \r\n                v-for=\"option in filter.options\" \r\n                :key=\"option.value\"\r\n                :value=\"option.value\"\r\n              >\r\n                {{ option.label }}\r\n              </option>\r\n            </select>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Botones de accin -->\r\n    <div class=\"row mb-3\" v-if=\"hasActions\">\r\n      <div class=\"col-12\">\r\n        <div class=\"btn-group\" role=\"group\">\r\n          <button \r\n            v-for=\"action in actions\"\r\n            :key=\"action.key\"\r\n            :class=\"`btn ${action.class || 'btn-primary'}`\"\r\n            @click=\"$emit('action', action.key)\"\r\n          >\r\n            <i :class=\"action.icon\" v-if=\"action.icon\"></i>\r\n            {{ action.label }}\r\n          </button>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Resumen de resultados -->\r\n    <div class=\"row mb-2\" v-if=\"showSummary\">\r\n      <div class=\"col-12\">\r\n        <small class=\"text-muted\">\r\n          {{ totalResults }} resultado(s) encontrado(s)\r\n          <span v-if=\"hasActiveFilters\">\r\n            - Filtrado por: {{ activeFiltersText }}\r\n          </span>\r\n        </small>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'SearchFilter',\r\n  props: {\r\n    searchPlaceholder: {\r\n      type: String,\r\n      default: 'Buscar...'\r\n    },\r\n    filters: {\r\n      type: Array,\r\n      default: () => []\r\n    },\r\n    actions: {\r\n      type: Array,\r\n      default: () => []\r\n    },\r\n    totalResults: {\r\n      type: Number,\r\n      default: 0\r\n    },\r\n    showSummary: {\r\n      type: Boolean,\r\n      default: true\r\n    },\r\n    searchDelay: {\r\n      type: Number,\r\n      default: 800\r\n    }\r\n  },\r\n  data() {\r\n    return {\r\n      searchQuery: '',\r\n      filterValues: {},\r\n      searchTimeout: null\r\n    };\r\n  },\r\n  computed: {\r\n    hasFilters() {\r\n      return this.filters.length > 0;\r\n    },\r\n    hasActions() {\r\n      return this.actions.length > 0;\r\n    },\r\n    hasActiveFilters() {\r\n      return Object.values(this.filterValues).some(value => value !== '');\r\n    },\r\n    activeFiltersText() {\r\n      const activeFilters = [];\r\n      for (const [key, value] of Object.entries(this.filterValues)) {\r\n        if (value !== '') {\r\n          const filter = this.filters.find(f => f.key === key);\r\n          const option = filter?.options.find(o => o.value === value);\r\n          if (option) {\r\n            activeFilters.push(`${filter.label}: ${option.label}`);\r\n          }\r\n        }\r\n      }\r\n      return activeFilters.join(', ');\r\n    }\r\n  },\r\n  mounted() {\r\n    // Inicializar valores de filtros\r\n    this.filters.forEach(filter => {\r\n      this.$set(this.filterValues, filter.key, '');\r\n    });\r\n  },\r\n  methods: {\r\n    onSearchInput() {\r\n      clearTimeout(this.searchTimeout);\r\n      this.searchTimeout = setTimeout(() => {\r\n        this.$emit('search', {\r\n          query: this.searchQuery,\r\n          filters: this.filterValues\r\n        });\r\n      }, this.searchDelay);\r\n    },\r\n    onFilterChange() {\r\n      this.$emit('search', {\r\n        query: this.searchQuery,\r\n        filters: this.filterValues\r\n      });\r\n    },\r\n    clearFilters() {\r\n      this.searchQuery = '';\r\n      Object.keys(this.filterValues).forEach(key => {\r\n        this.filterValues[key] = '';\r\n      });\r\n      this.$emit('search', {\r\n        query: '',\r\n        filters: this.filterValues\r\n      });\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.search-filter-container {\r\n  background: #f8f9fa;\r\n  padding: 1rem;\r\n  border-radius: 0.5rem;\r\n  margin-bottom: 1rem;\r\n}\r\n\r\n.input-group-text {\r\n  background-color: #e9ecef;\r\n  border-color: #ced4da;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
