___CSS_LOADER_EXPORT___.push([module.id, ".home-page[data-v-3afe9578]{padding:0}.content-header[data-v-3afe9578]{padding:15px 0}.small-box[data-v-3afe9578]{border-radius:10px;box-shadow:0 0 1px rgba(0,0,0,.125),0 1px 3px rgba(0,0,0,.2)}.btn-app[data-v-3afe9578]{border-radius:8px;height:auto;padding:15px 10px;text-align:center;width:100%}.card[data-v-3afe9578]{border:none;box-shadow:0 0 1px rgba(0,0,0,.125),0 1px 3px rgba(0,0,0,.2)}", "",{"version":3,"sources":["webpack://./resources/js/components/pages/HomePage.vue"],"names":[],"mappings":"AAiPA,4BACA,SACA,CAEA,iCACA,cACA,CAEA,4BACA,kBAAA,CACA,4DACA,CAEA,0BACA,iBAAA,CAIA,WAAA,CAFA,iBAAA,CADA,iBAAA,CAEA,UAEA,CAEA,uBACA,WAAA,CACA,4DACA","sourcesContent":["<template>\r\n    <div class=\"home-page\">\r\n        <!-- Header de bienvenida -->\r\n        <div class=\"content-header\">\r\n            <div class=\"container-fluid\">\r\n                <div class=\"row mb-2\">\r\n                    <div class=\"col-sm-6\">\r\n                        <h1 class=\"m-0\">Sistema BMX</h1>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </div>\r\n\r\n        <!-- Contenido principal -->\r\n        <section class=\"content\">\r\n            <div class=\"container-fluid\">\r\n                <!-- Estadsticas rpidas -->\r\n                <div class=\"row\">\r\n                    <div class=\"col-lg-3 col-6\">\r\n                        <div class=\"small-box bg-info\">\r\n                            <div class=\"inner\">\r\n                                <h3>{{ stats.pilots || 0 }}</h3>\r\n                                <p>Pilotos Registrados</p>\r\n                            </div>\r\n                            <div class=\"icon\">\r\n                                <i class=\"fas fa-user-ninja\"></i>\r\n                            </div>\r\n                            <router-link to=\"/pilotos\" class=\"small-box-footer\">\r\n                                Ver ms <i class=\"fas fa-arrow-circle-right\"></i>\r\n                            </router-link>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <div class=\"col-lg-3 col-6\">\r\n                        <div class=\"small-box bg-success\">\r\n                            <div class=\"inner\">\r\n                                <h3>{{ stats.clubs || 0 }}</h3>\r\n                                <p>Clubes</p>\r\n                            </div>\r\n                            <div class=\"icon\">\r\n                                <i class=\"fas fa-flag\"></i>\r\n                            </div>\r\n                            <router-link to=\"/clubes\" class=\"small-box-footer\">\r\n                                Ver ms <i class=\"fas fa-arrow-circle-right\"></i>\r\n                            </router-link>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <div class=\"col-lg-3 col-6\">\r\n                        <div class=\"small-box bg-warning\">\r\n                            <div class=\"inner\">\r\n                                <h3>{{ stats.championships || 0 }}</h3>\r\n                                <p>Campeonatos</p>\r\n                            </div>\r\n                            <div class=\"icon\">\r\n                                <i class=\"fas fa-trophy\"></i>\r\n                            </div>\r\n                            <router-link to=\"/campeonatos\" class=\"small-box-footer\">\r\n                                Ver ms <i class=\"fas fa-arrow-circle-right\"></i>\r\n                            </router-link>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <div class=\"col-lg-3 col-6\">\r\n                        <div class=\"small-box bg-danger\">\r\n                            <div class=\"inner\">\r\n                                <h3>{{ stats.matchdays || 0 }}</h3>\r\n                                <p>Jornadas</p>\r\n                            </div>\r\n                            <div class=\"icon\">\r\n                                <i class=\"fas fa-calendar\"></i>\r\n                            </div>\r\n                            <router-link to=\"/jornadas\" class=\"small-box-footer\">\r\n                                Ver ms <i class=\"fas fa-arrow-circle-right\"></i>\r\n                            </router-link>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n\r\n                <!-- Acciones rpidas -->\r\n                <div class=\"row\">\r\n                    <div class=\"col-md-6\">\r\n                        <div class=\"card\">\r\n                            <div class=\"card-header\">\r\n                                <h3 class=\"card-title\">\r\n                                    <i class=\"fas fa-tachometer-alt mr-1\"></i>\r\n                                    Acciones Rpidas\r\n                                </h3>\r\n                            </div>\r\n                            <div class=\"card-body\">\r\n                                <div class=\"row\">\r\n                                    <div class=\"col-6 mb-3\">\r\n                                        <router-link to=\"/pilotos\" class=\"btn btn-app\">\r\n                                            <i class=\"fas fa-user-ninja\"></i> Gestionar Pilotos\r\n                                        </router-link>\r\n                                    </div>\r\n                                    <div class=\"col-6 mb-3\">\r\n                                        <router-link to=\"/clubes\" class=\"btn btn-app\">\r\n                                            <i class=\"fas fa-flag\"></i> Gestionar Clubes\r\n                                        </router-link>\r\n                                    </div>\r\n                                    <div class=\"col-6 mb-3\">\r\n                                        <router-link to=\"/campeonatos\" class=\"btn btn-app\">\r\n                                            <i class=\"fas fa-trophy\"></i> Campeonatos\r\n                                        </router-link>\r\n                                    </div>\r\n                                    <div class=\"col-6 mb-3\">\r\n                                        <router-link to=\"/jornadas\" class=\"btn btn-app\">\r\n                                            <i class=\"fas fa-calendar\"></i> Jornadas\r\n                                        </router-link>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <div class=\"col-md-6\">\r\n                        <div class=\"card\">\r\n                            <div class=\"card-header\">\r\n                                <h3 class=\"card-title\">\r\n                                    <i class=\"fas fa-info-circle mr-1\"></i>\r\n                                    Informacin del Sistema\r\n                                </h3>\r\n                            </div>\r\n                            <div class=\"card-body\">\r\n                                <p><strong>Sistema de Puntuacin BMX</strong></p>\r\n                                <p>Gestiona pilotos, clubes, campeonatos y jornadas de manera eficiente.</p>\r\n                                <hr>\r\n                                <div v-if=\"currentUser\">\r\n                                    <p><strong>Usuario:</strong> {{ currentUser.name }}</p>\r\n                                    <p><strong>Email:</strong> {{ currentUser.email }}</p>\r\n                                </div>\r\n                                <div v-else>\r\n                                    <p>Visitando como invitado</p>\r\n                                    <a href=\"/login\" class=\"btn btn-primary btn-sm\">\r\n                                        <i class=\"fas fa-sign-in-alt\"></i> Iniciar Sesin\r\n                                    </a>\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </section>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    name: 'HomePage',\r\n    data() {\r\n        return {\r\n            stats: {\r\n                pilots: 0,\r\n                clubs: 0,\r\n                championships: 0,\r\n                matchdays: 0\r\n            },\r\n            loading: false\r\n        };\r\n    },\r\n    computed: {\r\n        currentUser() {\r\n            return window.Laravel?.user || null;\r\n        }\r\n    },\r\n    mounted() {\r\n        this.loadStats();\r\n    },\r\n    methods: {\r\n        async loadStats() {\r\n            this.loading = true;\r\n            try {\r\n                // Cargar estadsticas bsicas\r\n                const promises = [\r\n                    this.loadPilotsCount(),\r\n                    this.loadClubsCount(),\r\n                    this.loadChampionshipsCount(),\r\n                    this.loadMatchdaysCount()\r\n                ];\r\n\r\n                await Promise.allSettled(promises);\r\n            } catch (error) {\r\n                console.error('Error loading stats:', error);\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        async loadPilotsCount() {\r\n            try {\r\n                const response = await fetch('/api/pilots?count=true');\r\n                if (response.ok) {\r\n                    const data = await response.json();\r\n                    this.stats.pilots = data.total || data.length || 0;\r\n                }\r\n            } catch (error) {\r\n                console.error('Error loading pilots count:', error);\r\n            }\r\n        },\r\n\r\n        async loadClubsCount() {\r\n            try {\r\n                const response = await fetch('/api/clubs');\r\n                if (response.ok) {\r\n                    const data = await response.json();\r\n                    this.stats.clubs = Array.isArray(data) ? data.length : (data.data ? data.data.length : 0);\r\n                }\r\n            } catch (error) {\r\n                console.error('Error loading clubs count:', error);\r\n            }\r\n        },\r\n\r\n        async loadChampionshipsCount() {\r\n            try {\r\n                const response = await fetch('/api/championships');\r\n                if (response.ok) {\r\n                    const data = await response.json();\r\n                    this.stats.championships = Array.isArray(data) ? data.length : (data.data ? data.data.length : 0);\r\n                }\r\n            } catch (error) {\r\n                console.error('Error loading championships count:', error);\r\n            }\r\n        },\r\n\r\n        async loadMatchdaysCount() {\r\n            try {\r\n                const response = await fetch('/api/matchdays');\r\n                if (response.ok) {\r\n                    const data = await response.json();\r\n                    this.stats.matchdays = Array.isArray(data) ? data.length : (data.data ? data.data.length : 0);\r\n                }\r\n            } catch (error) {\r\n                console.error('Error loading matchdays count:', error);\r\n            }\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.home-page {\r\n    padding: 0;\r\n}\r\n\r\n.content-header {\r\n    padding: 15px 0;\r\n}\r\n\r\n.small-box {\r\n    border-radius: 10px;\r\n    box-shadow: 0 0 1px rgba(0,0,0,.125), 0 1px 3px rgba(0,0,0,.2);\r\n}\r\n\r\n.btn-app {\r\n    border-radius: 8px;\r\n    text-align: center;\r\n    padding: 15px 10px;\r\n    width: 100%;\r\n    height: auto;\r\n}\r\n\r\n.card {\r\n    border: none;\r\n    box-shadow: 0 0 1px rgba(0,0,0,.125), 0 1px 3px rgba(0,0,0,.2);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
