lass=\"spinnerClass\"></div>\r\n      <span class=\"spinner-text ml-2\" v-if=\"message\">{{ message }}</span>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'LoadingSpinner',\r\n  props: {\r\n    isLoading: {\r\n      type: Boolean,\r\n      default: false\r\n    },\r\n    message: {\r\n      type: String,\r\n      default: ''\r\n    },\r\n    overlay: {\r\n      type: Boolean,\r\n      default: false\r\n    },\r\n    size: {\r\n      type: String,\r\n      default: 'md', // sm, md, lg\r\n      validator: value => ['sm', 'md', 'lg'].includes(value)\r\n    },\r\n    color: {\r\n      type: String,\r\n      default: 'primary' // primary, secondary, success, danger, warning, info\r\n    }\r\n  },\r\n  computed: {\r\n    spinnerClass() {\r\n      return `spinner-border-${this.size} text-${this.color}`;\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.spinner-overlay {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 9999;\r\n}\r\n\r\n.spinner-container {\r\n  background: white;\r\n  padding: 2rem;\r\n  border-radius: 0.5rem;\r\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n  text-align: center;\r\n}\r\n\r\n.spinner-inline {\r\n  display: inline-flex;\r\n  align-items: center;\r\n}\r\n\r\n.spinner {\r\n  display: inline-block;\r\n  width: 2rem;\r\n  height: 2rem;\r\n  vertical-align: text-bottom;\r\n  border: 0.25em solid currentColor;\r\n  border-right-color: transparent;\r\n  border-radius: 50%;\r\n  animation: spinner-border 0.75s linear infinite;\r\n}\r\n\r\n.spinner-border-sm {\r\n  width: 1rem;\r\n  height: 1rem;\r\n  border-width: 0.2em;\r\n}\r\n\r\n.spinner-border-lg {\r\n  width: 3rem;\r\n  height: 3rem;\r\n  border-width: 0.3em;\r\n}\r\n\r\n.spinner-text {\r\n  color: #6c757d;\r\n  font-size: 0.9rem;\r\n}\r\n\r\n@keyframes spinner-border {\r\n  to {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
