import ___CSS_LOADER_API_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".user-avatar[data-v-1c9d356a]{font-family:Arial,sans-serif;font-weight:700}.text-truncate[data-v-1c9d356a]{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}pre[data-v-1c9d356a]{font-size:12px;max-height:200px;overflow-y:auto}.pagination .page-link[data-v-1c9d356a]{cursor:pointer}.pagination .page-item.disabled .page-link[data-v-1c9d356a]{cursor:not-allowed}", "",{"version":3,"sources":["webpack://./resources/js/components/ActivityLogManager.vue"],"names":[],"mappings":"AA8kBA,8BACA,4BAAA,CACA,eACA,CAEA,gCAEA,eAAA,CACA,sBAAA,CAFA,kBAGA,CAEA,qBACA,cAAA,CACA,gBAAA,CACA,eACA,CAEA,wCACA,cACA,CAEA,4DACA,kBACA","sourcesContent":["<template>\r\n  <div class=\"activity-log-manager\">\r\n    <!-- Header -->\r\n    <div class=\"content-header\">\r\n      <div class=\"container-fluid\">\r\n        <div class=\"row mb-2\">\r\n          <div class=\"col-sm-6\">\r\n            <h1 class=\"m-0\">\r\n              <i class=\"fas fa-history text-info\"></i>\r\n              Registro de Actividad\r\n            </h1>\r\n          </div>\r\n          <div class=\"col-sm-6\">\r\n            <ol class=\"breadcrumb float-sm-right\">\r\n              <li class=\"breadcrumb-item\">\r\n                <router-link to=\"/dashboard\">Inicio</router-link>\r\n              </li>\r\n              <li class=\"breadcrumb-item active\">Logs de Actividad</li>\r\n            </ol>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Content -->\r\n    <section class=\"content\">\r\n      <div class=\"container-fluid\">\r\n        <!-- Filters Card -->\r\n        <div class=\"card card-secondary\" :class=\"{ 'collapsed-card': !showFilters }\">\r\n          <div class=\"card-header\">\r\n            <h3 class=\"card-title\">\r\n              <i class=\"fas fa-filter\"></i>\r\n              Filtros\r\n            </h3>\r\n            <div class=\"card-tools\">\r\n              <button \r\n                type=\"button\" \r\n                class=\"btn btn-tool\" \r\n                @click=\"showFilters = !showFilters\"\r\n              >\r\n                <i :class=\"showFilters ? 'fas fa-minus' : 'fas fa-plus'\"></i>\r\n              </button>\r\n            </div>\r\n          </div>\r\n          <div class=\"card-body\" v-show=\"showFilters\">\r\n            <div class=\"row\">\r\n              <div class=\"col-md-3\">\r\n                <div class=\"form-group\">\r\n                  <label>Usuario</label>\r\n                  <select v-model=\"filters.user_id\" class=\"form-control\">\r\n                    <option value=\"\">Todos los usuarios</option>\r\n                    <option \r\n                      v-for=\"user in users\" \r\n                      :key=\"user.id\" \r\n                      :value=\"user.id\"\r\n                    >\r\n                      {{ user.name }}\r\n                    </option>\r\n                  </select>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-2\">\r\n                <div class=\"form-group\">\r\n                  <label>Accin</label>\r\n                  <select v-model=\"filters.action\" class=\"form-control\">\r\n                    <option value=\"\">Todas las acciones</option>\r\n                    <option \r\n                      v-for=\"action in actions\" \r\n                      :key=\"action\" \r\n                      :value=\"action\"\r\n                    >\r\n                      {{ capitalizeFirst(action) }}\r\n                    </option>\r\n                  </select>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-2\">\r\n                <div class=\"form-group\">\r\n                  <label>Tipo de Modelo</label>\r\n                  <select v-model=\"filters.model_type\" class=\"form-control\">\r\n                    <option value=\"\">Todos los tipos</option>\r\n                    <option \r\n                      v-for=\"type in modelTypes\" \r\n                      :key=\"type.value\" \r\n                      :value=\"type.value\"\r\n                    >\r\n                      {{ type.label }}\r\n                    </option>\r\n                  </select>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-2\">\r\n                <div class=\"form-group\">\r\n                  <label>Fecha Desde</label>\r\n                  <input \r\n                    type=\"date\" \r\n                    v-model=\"filters.date_from\" \r\n                    class=\"form-control\"\r\n                  >\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-2\">\r\n                <div class=\"form-group\">\r\n                  <label>Fecha Hasta</label>\r\n                  <input \r\n                    type=\"date\" \r\n                    v-model=\"filters.date_to\" \r\n                    class=\"form-control\"\r\n                  >\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-1\">\r\n                <div class=\"form-group\">\r\n                  <label>&nbsp;</label>\r\n                  <div class=\"btn-group-vertical d-block\">\r\n                    <button \r\n                      type=\"button\" \r\n                      @click=\"applyFilters\" \r\n                      class=\"btn btn-primary btn-sm\"\r\n                      :disabled=\"loading\"\r\n                    >\r\n                      <i class=\"fas fa-search\"></i>\r\n                    </button>\r\n                    <button \r\n                      type=\"button\" \r\n                      @click=\"clearFilters\" \r\n                      class=\"btn btn-secondary btn-sm\"\r\n                    >\r\n                      <i class=\"fas fa-times\"></i>\r\n                    </button>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n\r\n        <!-- Logs List Card -->\r\n        <div class=\"card\">\r\n          <div class=\"card-header\">\r\n            <h3 class=\"card-title\">\r\n              <i class=\"fas fa-list\"></i>\r\n              Actividad Reciente\r\n              <small class=\"text-muted\" v-if=\"pagination.total\">({{ pagination.total }} registros)</small>\r\n            </h3>\r\n            <div class=\"card-tools\">\r\n              <button \r\n                @click=\"exportLogs\" \r\n                class=\"btn btn-success btn-sm\"\r\n                :disabled=\"loading\"\r\n              >\r\n                <i class=\"fas fa-download\"></i> Exportar CSV\r\n              </button>\r\n            </div>\r\n          </div>\r\n          \r\n          <div class=\"card-body p-0\">\r\n            <!-- Loading State -->\r\n            <div v-if=\"loading\" class=\"text-center p-4\">\r\n              <i class=\"fas fa-spinner fa-spin fa-2x text-muted\"></i>\r\n              <p class=\"text-muted mt-2\">Cargando registros...</p>\r\n            </div>\r\n\r\n            <!-- Logs Table -->\r\n            <div v-else-if=\"logs.length > 0\" class=\"table-responsive\">\r\n              <table class=\"table table-sm table-hover\">\r\n                <thead>\r\n                  <tr>\r\n                    <th width=\"150\">Fecha</th>\r\n                    <th width=\"120\">Usuario</th>\r\n                    <th width=\"80\">Accin</th>\r\n                    <th width=\"100\">Tipo</th>\r\n                    <th>Descripcin</th>\r\n                    <th width=\"120\">IP</th>\r\n                    <th width=\"60\">Ver</th>\r\n                  </tr>\r\n                </thead>\r\n                <tbody>\r\n                  <tr v-for=\"log in logs\" :key=\"log.id\">\r\n                    <td>\r\n                      <small>{{ formatDate(log.created_at) }}</small>\r\n                    </td>\r\n                    <td>\r\n                      <div v-if=\"log.user\" class=\"d-flex align-items-center\">\r\n                        <div \r\n                          class=\"user-avatar bg-secondary text-white rounded-circle d-flex align-items-center justify-content-center mr-1\"\r\n                          style=\"width: 20px; height: 20px; font-size: 10px;\"\r\n                        >\r\n                          {{ getUserInitial(log.user.name) }}\r\n                        </div>\r\n                        <small>{{ log.user.name }}</small>\r\n                      </div>\r\n                      <small v-else class=\"text-muted\">Sistema</small>\r\n                    </td>\r\n                    <td>\r\n                      <span :class=\"getActionBadgeClass(log.action)\">\r\n                        {{ capitalizeFirst(log.action) }}\r\n                      </span>\r\n                    </td>\r\n                    <td>\r\n                      <small>{{ getModelBaseName(log.model_type) }}</small>\r\n                    </td>\r\n                    <td>\r\n                      <div \r\n                        class=\"text-truncate\" \r\n                        style=\"max-width: 300px;\" \r\n                        :title=\"log.description\"\r\n                      >\r\n                        {{ log.description }}\r\n                      </div>\r\n                    </td>\r\n                    <td>\r\n                      <small class=\"text-muted\">{{ log.ip_address }}</small>\r\n                    </td>\r\n                    <td>\r\n                      <button \r\n                        @click=\"showLogDetails(log)\" \r\n                        class=\"btn btn-info btn-xs\" \r\n                        title=\"Ver detalles\"\r\n                      >\r\n                        <i class=\"fas fa-eye\"></i>\r\n                      </button>\r\n                    </td>\r\n                  </tr>\r\n                </tbody>\r\n              </table>\r\n            </div>\r\n\r\n            <!-- Empty State -->\r\n            <div v-else class=\"text-center p-4\">\r\n              <i class=\"fas fa-history fa-3x text-muted mb-3\"></i>\r\n              <p class=\"text-muted\">No se encontraron registros de actividad.</p>\r\n            </div>\r\n          </div>\r\n          \r\n          <!-- Pagination -->\r\n          <div v-if=\"pagination.last_page > 1\" class=\"card-footer\">\r\n            <nav>\r\n              <ul class=\"pagination justify-content-center mb-0\">\r\n                <li class=\"page-item\" :class=\"{ disabled: pagination.current_page === 1 }\">\r\n                  <button \r\n                    class=\"page-link\" \r\n                    @click=\"changePage(pagination.current_page - 1)\"\r\n                    :disabled=\"pagination.current_page === 1\"\r\n                  >\r\n                    Anterior\r\n                  </button>\r\n                </li>\r\n                \r\n                <li \r\n                  v-for=\"page in getVisiblePages()\"\r\n                  :key=\"page\"\r\n                  class=\"page-item\" \r\n                  :class=\"{ active: page === pagination.current_page }\"\r\n                >\r\n                  <button class=\"page-link\" @click=\"changePage(page)\">\r\n                    {{ page }}\r\n                  </button>\r\n                </li>\r\n                \r\n                <li class=\"page-item\" :class=\"{ disabled: pagination.current_page === pagination.last_page }\">\r\n                  <button \r\n                    class=\"page-link\" \r\n                    @click=\"changePage(pagination.current_page + 1)\"\r\n                    :disabled=\"pagination.current_page === pagination.last_page\"\r\n                  >\r\n                    Siguiente\r\n                  </button>\r\n                </li>\r\n              </ul>\r\n            </nav>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </section>\r\n\r\n    <!-- Log Details Modal -->\r\n    <div \r\n      class=\"modal fade\" \r\n      id=\"logDetailsModal\" \r\n      tabindex=\"-1\" \r\n      role=\"dialog\"\r\n      ref=\"logDetailsModal\"\r\n    >\r\n      <div class=\"modal-dialog modal-lg\" role=\"document\">\r\n        <div class=\"modal-content\">\r\n          <div class=\"modal-header\">\r\n            <h5 class=\"modal-title\">\r\n              <i class=\"fas fa-info-circle\"></i>\r\n              Detalles del Registro de Actividad\r\n            </h5>\r\n            <button type=\"button\" class=\"close\" @click=\"closeLogDetails\">\r\n              <span>&times;</span>\r\n            </button>\r\n          </div>\r\n          <div class=\"modal-body\" v-if=\"selectedLog\">\r\n            <div class=\"row\">\r\n              <div class=\"col-md-6\">\r\n                <strong>Fecha:</strong>\r\n                <p>{{ formatDateTime(selectedLog.created_at) }}</p>\r\n              </div>\r\n              <div class=\"col-md-6\">\r\n                <strong>Usuario:</strong>\r\n                <p>{{ selectedLog.user ? selectedLog.user.name : 'Sistema' }}</p>\r\n              </div>\r\n              <div class=\"col-md-6\">\r\n                <strong>Accin:</strong>\r\n                <p>\r\n                  <span :class=\"getActionBadgeClass(selectedLog.action)\">\r\n                    {{ capitalizeFirst(selectedLog.action) }}\r\n                  </span>\r\n                </p>\r\n              </div>\r\n              <div class=\"col-md-6\">\r\n                <strong>Tipo de Modelo:</strong>\r\n                <p>{{ getModelBaseName(selectedLog.model_type) }}</p>\r\n              </div>\r\n              <div class=\"col-md-6\">\r\n                <strong>ID del Modelo:</strong>\r\n                <p>{{ selectedLog.model_id }}</p>\r\n              </div>\r\n              <div class=\"col-md-6\">\r\n                <strong>IP:</strong>\r\n                <p>{{ selectedLog.ip_address }}</p>\r\n              </div>\r\n              <div class=\"col-12\">\r\n                <strong>Descripcin:</strong>\r\n                <p>{{ selectedLog.description }}</p>\r\n              </div>\r\n              <div class=\"col-12\" v-if=\"selectedLog.user_agent\">\r\n                <strong>User Agent:</strong>\r\n                <p class=\"text-muted small\">{{ selectedLog.user_agent }}</p>\r\n              </div>\r\n            </div>\r\n            \r\n            <!-- Old Values -->\r\n            <div v-if=\"selectedLog.old_values && Object.keys(selectedLog.old_values).length > 0\">\r\n              <hr>\r\n              <h6><i class=\"fas fa-history\"></i> Valores Anteriores:</h6>\r\n              <div class=\"bg-light p-3 rounded\">\r\n                <pre>{{ JSON.stringify(selectedLog.old_values, null, 2) }}</pre>\r\n              </div>\r\n            </div>\r\n            \r\n            <!-- New Values -->\r\n            <div v-if=\"selectedLog.new_values && Object.keys(selectedLog.new_values).length > 0\">\r\n              <hr>\r\n              <h6><i class=\"fas fa-plus-circle\"></i> Valores Nuevos:</h6>\r\n              <div class=\"bg-light p-3 rounded\">\r\n                <pre>{{ JSON.stringify(selectedLog.new_values, null, 2) }}</pre>\r\n              </div>\r\n            </div>\r\n          </div>\r\n          <div class=\"modal-footer\">\r\n            <button type=\"button\" class=\"btn btn-secondary\" @click=\"closeLogDetails\">\r\n              Cerrar\r\n            </button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'ActivityLogManager',\r\n  \r\n  data() {\r\n    return {\r\n      logs: [],\r\n      users: [],\r\n      actions: [],\r\n      modelTypes: [],\r\n      loading: false,\r\n      showFilters: false,\r\n      selectedLog: null,\r\n      \r\n      filters: {\r\n        user_id: '',\r\n        action: '',\r\n        model_type: '',\r\n        date_from: '',\r\n        date_to: ''\r\n      },\r\n      \r\n      pagination: {\r\n        current_page: 1,\r\n        last_page: 1,\r\n        per_page: 50,\r\n        total: 0\r\n      }\r\n    }\r\n  },\r\n  \r\n  mounted() {\r\n    this.loadData();\r\n  },\r\n  \r\n  methods: {\r\n    async loadData() {\r\n      this.loading = true;\r\n      try {\r\n        await Promise.all([\r\n          this.loadLogs(),\r\n          this.loadFilterData()\r\n        ]);\r\n      } catch (error) {\r\n        console.error('Error loading data:', error);\r\n        this.$toast.error('Error al cargar los datos');\r\n      } finally {\r\n        this.loading = false;\r\n      }\r\n    },\r\n    \r\n    async loadLogs(page = 1) {\r\n      try {\r\n        const params = new URLSearchParams({\r\n          page: page,\r\n          ...this.filters\r\n        });\r\n        \r\n        // Remove empty filters\r\n        for (const [key, value] of params.entries()) {\r\n          if (!value || value === '') {\r\n            params.delete(key);\r\n          }\r\n        }\r\n        \r\n        const response = await fetch(`/api/activity-logs?${params}`);\r\n        if (!response.ok) {\r\n          throw new Error('Failed to load logs');\r\n        }\r\n        \r\n        const data = await response.json();\r\n        this.logs = data.data;\r\n        this.pagination = {\r\n          current_page: data.current_page,\r\n          last_page: data.last_page,\r\n          per_page: data.per_page,\r\n          total: data.total\r\n        };\r\n      } catch (error) {\r\n        console.error('Error loading logs:', error);\r\n        this.$toast.error('Error al cargar los registros de actividad');\r\n      }\r\n    },\r\n    \r\n    async loadFilterData() {\r\n      try {\r\n        const response = await fetch('/api/activity-logs/filter-data');\r\n        if (!response.ok) {\r\n          throw new Error('Failed to load filter data');\r\n        }\r\n        \r\n        const data = await response.json();\r\n        this.users = data.users;\r\n        this.actions = data.actions;\r\n        this.modelTypes = data.modelTypes;\r\n      } catch (error) {\r\n        console.error('Error loading filter data:', error);\r\n      }\r\n    },\r\n    \r\n    applyFilters() {\r\n      this.pagination.current_page = 1;\r\n      this.loadLogs(1);\r\n    },\r\n    \r\n    clearFilters() {\r\n      this.filters = {\r\n        user_id: '',\r\n        action: '',\r\n        model_type: '',\r\n        date_from: '',\r\n        date_to: ''\r\n      };\r\n      this.applyFilters();\r\n    },\r\n    \r\n    changePage(page) {\r\n      if (page >= 1 && page <= this.pagination.last_page) {\r\n        this.loadLogs(page);\r\n      }\r\n    },\r\n    \r\n    getVisiblePages() {\r\n      const current = this.pagination.current_page;\r\n      const last = this.pagination.last_page;\r\n      const delta = 2;\r\n      const range = [];\r\n      \r\n      for (let i = Math.max(2, current - delta); \r\n           i <= Math.min(last - 1, current + delta); \r\n           i++) {\r\n        range.push(i);\r\n      }\r\n      \r\n      if (current - delta > 2) {\r\n        range.unshift('...');\r\n      }\r\n      if (current + delta < last - 1) {\r\n        range.push('...');\r\n      }\r\n      \r\n      range.unshift(1);\r\n      if (last !== 1) {\r\n        range.push(last);\r\n      }\r\n      \r\n      return range.filter((v, i, a) => a.indexOf(v) === i);\r\n    },\r\n    \r\n    showLogDetails(log) {\r\n      this.selectedLog = log;\r\n      $(this.$refs.logDetailsModal).modal('show');\r\n    },\r\n    \r\n    closeLogDetails() {\r\n      $(this.$refs.logDetailsModal).modal('hide');\r\n      this.selectedLog = null;\r\n    },\r\n    \r\n    async exportLogs() {\r\n      try {\r\n        const params = new URLSearchParams(this.filters);\r\n        \r\n        // Remove empty filters\r\n        for (const [key, value] of params.entries()) {\r\n          if (!value || value === '') {\r\n            params.delete(key);\r\n          }\r\n        }\r\n        \r\n        const url = `/api/activity-logs/export?${params}`;\r\n        window.open(url, '_blank');\r\n      } catch (error) {\r\n        console.error('Error exporting logs:', error);\r\n        this.$toast.error('Error al exportar los registros');\r\n      }\r\n    },\r\n    \r\n    formatDate(dateString) {\r\n      return new Date(dateString).toLocaleString('es-ES', {\r\n        day: '2-digit',\r\n        month: '2-digit',\r\n        year: 'numeric',\r\n        hour: '2-digit',\r\n        minute: '2-digit',\r\n        second: '2-digit'\r\n      });\r\n    },\r\n    \r\n    formatDateTime(dateString) {\r\n      return new Date(dateString).toLocaleString('es-ES', {\r\n        weekday: 'long',\r\n        day: '2-digit',\r\n        month: 'long',\r\n        year: 'numeric',\r\n        hour: '2-digit',\r\n        minute: '2-digit',\r\n        second: '2-digit'\r\n      });\r\n    },\r\n    \r\n    getUserInitial(name) {\r\n      return name ? name.charAt(0).toUpperCase() : '?';\r\n    },\r\n    \r\n    getActionBadgeClass(action) {\r\n      const classes = {\r\n        create: 'badge badge-success',\r\n        update: 'badge badge-warning',\r\n        delete: 'badge badge-danger'\r\n      };\r\n      return classes[action] || 'badge badge-info';\r\n    },\r\n    \r\n    getModelBaseName(modelType) {\r\n      return modelType ? modelType.split('\\\\').pop() : '';\r\n    },\r\n    \r\n    capitalizeFirst(str) {\r\n      return str ? str.charAt(0).toUpperCase() + str.slice(1) : '';\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.user-avatar {\r\n  font-family: Arial, sans-serif;\r\n  font-weight: bold;\r\n}\r\n\r\n.text-truncate {\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\npre {\r\n  font-size: 12px;\r\n  max-height: 200px;\r\n  overflow-y: auto;\r\n}\r\n\r\n.pagination .page-link {\r\n  cursor: pointer;\r\n}\r\n\r\n.pagination .page-item.disabled .page-link {\r\n  cursor: not-allowed;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
