= this.matchdays.filter(m => m.id !== this.matchdayToDelete.id);\r\n          this.calculateStats();\r\n          this.$refs.notifications.show('Jornada eliminada correctamente', 'success');\r\n          $('#deleteModal').modal('hide');\r\n        } else {\r\n          const error = await response.json();\r\n          this.$refs.notifications.show(error.message || 'Error al eliminar', 'error');\r\n        }\r\n      } catch (error) {\r\n        console.error('Error:', error);\r\n        this.$refs.notifications.show('Error de conexin', 'error');\r\n      } finally {\r\n        this.deleting = false;\r\n        this.matchdayToDelete = null;\r\n      }\r\n    },\r\n    \r\n    updateURL() {\r\n      const params = new URLSearchParams();\r\n      if (this.championshipFilter) params.set('championship', this.championshipFilter);\r\n      if (this.statusFilter) params.set('status', this.statusFilter);\r\n      \r\n      const newURL = window.location.pathname + (params.toString() ? '?' + params.toString() : '');\r\n      window.history.replaceState({}, '', newURL);\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.matchday-card {\r\n  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;\r\n  border: none;\r\n  box-shadow: 0 2px 8px rgba(0,0,0,0.1);\r\n}\r\n\r\n.matchday-card:hover {\r\n  transform: translateY(-5px);\r\n  box-shadow: 0 8px 25px rgba(0,0,0,0.15);\r\n}\r\n\r\n.info-item {\r\n  display: flex;\r\n  align-items: flex-start;\r\n  gap: 8px;\r\n}\r\n\r\n.info-item i {\r\n  margin-top: 2px;\r\n  width: 16px;\r\n  flex-shrink: 0;\r\n}\r\n\r\n.badge-lg {\r\n  padding: 0.5rem 0.75rem;\r\n  font-size: 0.95rem;\r\n  font-weight: 600;\r\n}\r\n\r\n.bg-gradient-primary {\r\n  background: linear-gradient(135deg, #007bff 0%, #6610f2 100%);\r\n}\r\n\r\n.bg-gradient-success {\r\n  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);\r\n}\r\n\r\n.bg-gradient-danger {\r\n  background: linear-gradient(135deg, #dc3545 0%, #fd7e14 100%);\r\n}\r\n