import ___CSS_LOADER_API_IMPORT___ from "../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".sortable[data-v-0b7a90dc]{cursor:pointer;-webkit-user-select:none;-moz-user-select:none;user-select:none}.sortable[data-v-0b7a90dc]:hover{background-color:rgba(0,0,0,.05)}.table-controls[data-v-0b7a90dc]{background:#f8f9fa;border-radius:5px;margin-bottom:20px;padding:15px}.pagination-wrapper[data-v-0b7a90dc]{border-top:1px solid #dee2e6;margin-top:20px;padding-top:20px}.export-buttons[data-v-0b7a90dc]{border-top:1px solid #dee2e6;padding-top:15px}", "",{"version":3,"sources":["webpack://./resources/js/components/DataTable.vue"],"names":[],"mappings":"AAiUA,2BACA,cAAA,CACA,wBAAA,CAAA,qBAAA,CAAA,gBACA,CAEA,iCACA,gCACA,CAEA,iCACA,kBAAA,CAEA,iBAAA,CACA,kBAAA,CAFA,YAGA,CAEA,qCAGA,4BAAA,CAFA,eAAA,CACA,gBAEA,CAEA,iCAEA,4BAAA,CADA,gBAEA","sourcesContent":["<template>\r\n  <div class=\"data-table-wrapper\">\r\n    <div class=\"table-controls mb-3\">\r\n      <div class=\"row\">\r\n        <div class=\"col-md-6\">\r\n          <div class=\"form-group\">\r\n            <label>Mostrar:</label>\r\n            <select v-model=\"pageSize\" @change=\"updatePageSize\" class=\"form-control form-control-sm d-inline-block ml-2\" style=\"width: auto;\">\r\n              <option value=\"10\">10</option>\r\n              <option value=\"25\">25</option>\r\n              <option value=\"50\">50</option>\r\n              <option value=\"100\">100</option>\r\n            </select>\r\n            <span class=\"ml-2\">registros</span>\r\n          </div>\r\n        </div>\r\n        <div class=\"col-md-6\">\r\n          <div class=\"form-group\">\r\n            <label>Buscar:</label>\r\n            <input \r\n              v-model=\"searchTerm\" \r\n              @input=\"search\"\r\n              type=\"text\" \r\n              class=\"form-control form-control-sm d-inline-block ml-2\" \r\n              style=\"width: 200px;\"\r\n              placeholder=\"Filtrar resultados...\"\r\n            >\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <div class=\"table-responsive\">\r\n      <table class=\"table table-striped table-hover\">\r\n        <thead class=\"thead-dark\">\r\n          <tr>\r\n            <th \r\n              v-for=\"column in columns\" \r\n              :key=\"column.key\"\r\n              @click=\"sort(column.key)\"\r\n              :class=\"{ 'sortable': column.sortable !== false }\"\r\n            >\r\n              {{ column.label }}\r\n              <i \r\n                v-if=\"column.sortable !== false\"\r\n                :class=\"getSortIcon(column.key)\"\r\n                class=\"ml-1\"\r\n              ></i>\r\n            </th>\r\n            <th v-if=\"actions.length > 0\">Acciones</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr v-for=\"(item, index) in paginatedData\" :key=\"getItemKey(item, index)\">\r\n            <td v-for=\"column in columns\" :key=\"column.key\">\r\n              <slot \r\n                :name=\"`column-${column.key}`\" \r\n                :item=\"item\" \r\n                :value=\"getNestedValue(item, column.key)\"\r\n              >\r\n                {{ formatValue(getNestedValue(item, column.key), column.format) }}\r\n              </slot>\r\n            </td>\r\n            <td v-if=\"actions.length > 0\">\r\n              <div class=\"btn-group btn-group-sm\">\r\n                <button\r\n                  v-for=\"action in actions\"\r\n                  :key=\"action.name\"\r\n                  @click=\"executeAction(action, item)\"\r\n                  :class=\"['btn', `btn-${action.variant || 'primary'}`]\"\r\n                  :title=\"action.title\"\r\n                >\r\n                  <i :class=\"action.icon\"></i>\r\n                  {{ action.label }}\r\n                </button>\r\n              </div>\r\n            </td>\r\n          </tr>\r\n          \r\n          <!-- Mensaje cuando no hay datos -->\r\n          <tr v-if=\"filteredData.length === 0\">\r\n            <td :colspan=\"columns.length + (actions.length > 0 ? 1 : 0)\" class=\"text-center text-muted py-4\">\r\n              {{ searchTerm ? 'No se encontraron resultados para la bsqueda' : 'No hay datos disponibles' }}\r\n            </td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n\r\n    <!-- Paginacin -->\r\n    <div v-if=\"totalPages > 1\" class=\"pagination-wrapper d-flex justify-content-between align-items-center\">\r\n      <div class=\"pagination-info\">\r\n        Mostrando {{ startRecord }} a {{ endRecord }} de {{ filteredData.length }} registros\r\n        <span v-if=\"searchTerm\">(filtrados de {{ data.length }} registros totales)</span>\r\n      </div>\r\n      \r\n      <nav>\r\n        <ul class=\"pagination pagination-sm mb-0\">\r\n          <li :class=\"['page-item', { disabled: currentPage === 1 }]\">\r\n            <button @click=\"goToPage(currentPage - 1)\" class=\"page-link\">\r\n              <i class=\"fas fa-chevron-left\"></i>\r\n            </button>\r\n          </li>\r\n          \r\n          <li \r\n            v-for=\"page in visiblePages\" \r\n            :key=\"page\"\r\n            :class=\"['page-item', { active: page === currentPage }]\"\r\n          >\r\n            <button @click=\"goToPage(page)\" class=\"page-link\">\r\n              {{ page }}\r\n            </button>\r\n          </li>\r\n          \r\n          <li :class=\"['page-item', { disabled: currentPage === totalPages }]\">\r\n            <button @click=\"goToPage(currentPage + 1)\" class=\"page-link\">\r\n              <i class=\"fas fa-chevron-right\"></i>\r\n            </button>\r\n          </li>\r\n        </ul>\r\n      </nav>\r\n    </div>\r\n\r\n    <!-- Botones de exportacin -->\r\n    <div v-if=\"exportable\" class=\"export-buttons mt-3\">\r\n      <button @click=\"exportData('csv')\" class=\"btn btn-sm btn-outline-primary mr-2\">\r\n        <i class=\"fas fa-file-csv\"></i> Exportar CSV\r\n      </button>\r\n      <button @click=\"exportData('excel')\" class=\"btn btn-sm btn-outline-success mr-2\">\r\n        <i class=\"fas fa-file-excel\"></i> Exportar Excel\r\n      </button>\r\n      <button @click=\"exportData('pdf')\" class=\"btn btn-sm btn-outline-danger\">\r\n        <i class=\"fas fa-file-pdf\"></i> Exportar PDF\r\n      </button>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'DataTable',\r\n  props: {\r\n    data: {\r\n      type: Array,\r\n      required: true\r\n    },\r\n    columns: {\r\n      type: Array,\r\n      required: true\r\n    },\r\n    actions: {\r\n      type: Array,\r\n      default: () => []\r\n    },\r\n    exportable: {\r\n      type: Boolean,\r\n      default: false\r\n    },\r\n    keyField: {\r\n      type: String,\r\n      default: 'id'\r\n    }\r\n  },\r\n  data() {\r\n    return {\r\n      searchTerm: '',\r\n      sortColumn: '',\r\n      sortDirection: 'asc',\r\n      currentPage: 1,\r\n      pageSize: 25,\r\n      filteredData: [],\r\n      loading: false\r\n    }\r\n  },\r\n  computed: {\r\n    totalPages() {\r\n      return Math.ceil(this.filteredData.length / this.pageSize);\r\n    },\r\n    \r\n    startRecord() {\r\n      return (this.currentPage - 1) * this.pageSize + 1;\r\n    },\r\n    \r\n    endRecord() {\r\n      const end = this.currentPage * this.pageSize;\r\n      return end > this.filteredData.length ? this.filteredData.length : end;\r\n    },\r\n    \r\n    paginatedData() {\r\n      const start = (this.currentPage - 1) * this.pageSize;\r\n      const end = start + this.pageSize;\r\n      return this.filteredData.slice(start, end);\r\n    },\r\n    \r\n    visiblePages() {\r\n      const pages = [];\r\n      const start = Math.max(1, this.currentPage - 2);\r\n      const end = Math.min(this.totalPages, this.currentPage + 2);\r\n      \r\n      for (let i = start; i <= end; i++) {\r\n        pages.push(i);\r\n      }\r\n      \r\n      return pages;\r\n    }\r\n  },\r\n  watch: {\r\n    data: {\r\n      handler() {\r\n        this.filterAndSort();\r\n      },\r\n      immediate: true,\r\n      deep: true\r\n    }\r\n  },\r\n  methods: {\r\n    search() {\r\n      this.currentPage = 1;\r\n      this.filterAndSort();\r\n    },\r\n    \r\n    sort(column) {\r\n      if (this.sortColumn === column) {\r\n        this.sortDirection = this.sortDirection === 'asc' ? 'desc' : 'asc';\r\n      } else {\r\n        this.sortColumn = column;\r\n        this.sortDirection = 'asc';\r\n      }\r\n      \r\n      this.filterAndSort();\r\n    },\r\n    \r\n    filterAndSort() {\r\n      let filtered = [...this.data];\r\n      \r\n      // Filtrar\r\n      if (this.searchTerm) {\r\n        const term = this.searchTerm.toLowerCase();\r\n        filtered = filtered.filter(item => {\r\n          return this.columns.some(column => {\r\n            const value = this.getNestedValue(item, column.key);\r\n            return String(value).toLowerCase().includes(term);\r\n          });\r\n        });\r\n      }\r\n      \r\n      // Ordenar\r\n      if (this.sortColumn) {\r\n        filtered.sort((a, b) => {\r\n          const aVal = this.getNestedValue(a, this.sortColumn);\r\n          const bVal = this.getNestedValue(b, this.sortColumn);\r\n          \r\n          let comparison = 0;\r\n          if (aVal > bVal) comparison = 1;\r\n          if (aVal < bVal) comparison = -1;\r\n          \r\n          return this.sortDirection === 'desc' ? -comparison : comparison;\r\n        });\r\n      }\r\n      \r\n      this.filteredData = filtered;\r\n    },\r\n    \r\n    getNestedValue(obj, path) {\r\n      return path.split('.').reduce((current, key) => current?.[key], obj);\r\n    },\r\n    \r\n    formatValue(value, format) {\r\n      if (!format) return value;\r\n      \r\n      switch (format) {\r\n        case 'date':\r\n          return new Date(value).toLocaleDateString();\r\n        case 'datetime':\r\n          return new Date(value).toLocaleString();\r\n        case 'currency':\r\n          return new Intl.NumberFormat('es-ES', { \r\n            style: 'currency', \r\n            currency: 'EUR' \r\n          }).format(value);\r\n        default:\r\n          return value;\r\n      }\r\n    },\r\n    \r\n    getSortIcon(column) {\r\n      if (this.sortColumn !== column) {\r\n        return 'fas fa-sort text-muted';\r\n      }\r\n      \r\n      return this.sortDirection === 'asc' \r\n        ? 'fas fa-sort-up text-primary' \r\n        : 'fas fa-sort-down text-primary';\r\n    },\r\n    \r\n    goToPage(page) {\r\n      if (page >= 1 && page <= this.totalPages) {\r\n        this.currentPage = page;\r\n      }\r\n    },\r\n    \r\n    updatePageSize() {\r\n      this.currentPage = 1;\r\n    },\r\n    \r\n    executeAction(action, item) {\r\n      this.$emit('action', { action: action.name, item });\r\n    },\r\n    \r\n    getItemKey(item, index) {\r\n      return item[this.keyField] || index;\r\n    },\r\n    \r\n    exportData(format) {\r\n      this.$emit('export', { format, data: this.filteredData });\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.sortable {\r\n  cursor: pointer;\r\n  user-select: none;\r\n}\r\n\r\n.sortable:hover {\r\n  background-color: rgba(0, 0, 0, 0.05);\r\n}\r\n\r\n.table-controls {\r\n  background: #f8f9fa;\r\n  padding: 15px;\r\n  border-radius: 5px;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.pagination-wrapper {\r\n  margin-top: 20px;\r\n  padding-top: 20px;\r\n  border-top: 1px solid #dee2e6;\r\n}\r\n\r\n.export-buttons {\r\n  padding-top: 15px;\r\n  border-top: 1px solid #dee2e6;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
