// Module
___CSS_LOADER_EXPORT___.push([module.id, ".card-tools .dropdown-menu[data-v-8cafca02]{border:1px solid #dee2e6;box-shadow:0 .125rem .25rem rgba(0,0,0,.075)}.form-group label[data-v-8cafca02]{color:#495057;font-weight:600}.custom-control-label[data-v-8cafca02]:before{border-radius:.375rem}.custom-control-input:checked~.custom-control-label[data-v-8cafca02]:before{background-color:#007bff;border-color:#007bff}.invalid-feedback[data-v-8cafca02]{font-size:.875em}.modal-header.bg-danger .close[data-v-8cafca02]{opacity:.8}.modal-header.bg-danger .close[data-v-8cafca02]:hover{opacity:1}.custom-file-label.selected[data-v-8cafca02]{color:#495057}.dropdown-item[data-v-8cafca02]:disabled{background-color:transparent;color:#6c757d;pointer-events:none}", "",{"version":3,"sources":["webpack://./resources/js/components/SettingsManager.vue"],"names":[],"mappings":"AAqkBA,4CACA,wBAAA,CACA,4CACA,CAEA,mCAEA,aAAA,CADA,eAEA,CAEA,8CACA,qBACA,CAEA,4EACA,wBAAA,CACA,oBACA,CAEA,mCACA,gBACA,CAEA,gDACA,UACA,CAEA,sDACA,SACA,CAEA,6CACA,aACA,CAEA,yCAGA,4BAAA,CAFA,aAAA,CACA,mBAEA","sourcesContent":["<template>\r\n  <div class=\"container-fluid\">\r\n    <!-- Header -->\r\n    <div class=\"content-header\">\r\n      <div class=\"container-fluid\">\r\n        <div class=\"row mb-2\">\r\n          <div class=\"col-sm-6\">\r\n            <h1>\r\n              <i class=\"fas fa-cogs mr-2\"></i>\r\n              Configuracin del Sistema\r\n            </h1>\r\n          </div>\r\n          <div class=\"col-sm-6\">\r\n            <ol class=\"breadcrumb float-sm-right\">\r\n              <li class=\"breadcrumb-item\"><a href=\"/admin/dashboard\">Dashboard</a></li>\r\n              <li class=\"breadcrumb-item active\">Configuracin</li>\r\n            </ol>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Content -->\r\n    <section class=\"content\">\r\n      <div class=\"container-fluid\">\r\n        <div class=\"row\">\r\n          <div class=\"col-12\">\r\n            <!-- Header Card -->\r\n            <div class=\"card card-primary\">\r\n              <div class=\"card-header\">\r\n                <h3 class=\"card-title\">\r\n                  <i class=\"fas fa-cogs mr-2\"></i>\r\n                  Configuracin del Sistema\r\n                </h3>\r\n                <div class=\"card-tools\">\r\n                  <div class=\"btn-group\">\r\n                    <button type=\"button\" \r\n                            class=\"btn btn-tool dropdown-toggle\" \r\n                            data-toggle=\"dropdown\"\r\n                            :disabled=\"loading\">\r\n                      <i class=\"fas fa-ellipsis-v\"></i>\r\n                    </button>\r\n                    <div class=\"dropdown-menu dropdown-menu-right\" role=\"menu\">\r\n                      <button @click=\"exportSettings\" class=\"dropdown-item\" :disabled=\"loading\">\r\n                        <i class=\"fas fa-download mr-2\"></i> Exportar Configuracin\r\n                      </button>\r\n                      <button @click=\"showImportModal\" class=\"dropdown-item\" :disabled=\"loading\">\r\n                        <i class=\"fas fa-upload mr-2\"></i> Importar Configuracin\r\n                      </button>\r\n                      <div class=\"dropdown-divider\"></div>\r\n                      <button @click=\"showResetModal\" \r\n                              class=\"dropdown-item text-danger\" \r\n                              :disabled=\"loading\">\r\n                        <i class=\"fas fa-undo mr-2\"></i> Restablecer Todo\r\n                      </button>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n\r\n            <!-- Notificaciones -->\r\n            <notification-system ref=\"notifications\"></notification-system>\r\n\r\n            <!-- Loading -->\r\n            <loading-spinner v-if=\"loading && !settingsLoaded\"></loading-spinner>\r\n\r\n            <!-- Settings Form -->\r\n            <form @submit.prevent=\"saveSettings\" v-if=\"!loading || settingsLoaded\">\r\n              <div v-for=\"(groupSettings, groupName) in settingsByGroup\" \r\n                   :key=\"groupName\" \r\n                   class=\"card card-outline card-secondary\">\r\n                <div class=\"card-header\">\r\n                  <h3 class=\"card-title\">\r\n                    <i :class=\"getGroupIcon(groupName)\" class=\"mr-2\"></i>\r\n                    {{ getGroupTitle(groupName) }}\r\n                  </h3>\r\n                  <div class=\"card-tools\">\r\n                    <button type=\"button\" \r\n                            class=\"btn btn-tool\" \r\n                            @click=\"toggleGroup(groupName)\"\r\n                            :disabled=\"loading\">\r\n                      <i :class=\"groupCollapsed[groupName] ? 'fas fa-plus' : 'fas fa-minus'\"></i>\r\n                    </button>\r\n                  </div>\r\n                </div>\r\n                <div v-show=\"!groupCollapsed[groupName]\" class=\"card-body\">\r\n                  <div class=\"row\">\r\n                    <div v-for=\"setting in groupSettings\" \r\n                         :key=\"setting.id\" \r\n                         class=\"col-md-6 mb-3\">\r\n                      <div class=\"form-group\">\r\n                        <label :for=\"`setting_${setting.id}`\">\r\n                          {{ setting.description || setting.key }}\r\n                          <span v-if=\"setting.type === 'password'\" class=\"text-muted small\">\r\n                            (Dejar vaco para mantener actual)\r\n                          </span>\r\n                        </label>\r\n                        \r\n                        <!-- Boolean (Switch) -->\r\n                        <div v-if=\"setting.type === 'boolean'\" class=\"custom-control custom-switch\">\r\n                          <input type=\"checkbox\" \r\n                                 class=\"custom-control-input\" \r\n                                 :id=\"`setting_${setting.id}`\" \r\n                                 v-model=\"settingValues[setting.key]\"\r\n                                 :true-value=\"true\"\r\n                                 :false-value=\"false\"\r\n                                 :disabled=\"loading\">\r\n                          <label class=\"custom-control-label\" :for=\"`setting_${setting.id}`\"></label>\r\n                        </div>\r\n                        \r\n                        <!-- Select -->\r\n                        <select v-else-if=\"setting.type === 'select'\" \r\n                                class=\"form-control\" \r\n                                :id=\"`setting_${setting.id}`\" \r\n                                v-model=\"settingValues[setting.key]\"\r\n                                :disabled=\"loading\">\r\n                          <option v-for=\"option in setting.options\" \r\n                                  :key=\"option\" \r\n                                  :value=\"option\">\r\n                            {{ formatOptionLabel(option) }}\r\n                          </option>\r\n                        </select>\r\n                        \r\n                        <!-- Number -->\r\n                        <input v-else-if=\"setting.type === 'number' || setting.type === 'integer'\" \r\n                               type=\"number\" \r\n                               class=\"form-control\" \r\n                               :id=\"`setting_${setting.id}`\" \r\n                               v-model=\"settingValues[setting.key]\"\r\n                               step=\"0.01\"\r\n                               :disabled=\"loading\">\r\n                        \r\n                        <!-- Password -->\r\n                        <input v-else-if=\"setting.type === 'password'\" \r\n                               type=\"password\" \r\n                               class=\"form-control\" \r\n                               :id=\"`setting_${setting.id}`\" \r\n                               v-model=\"settingValues[setting.key]\"\r\n                               placeholder=\"\"\r\n                               :disabled=\"loading\">\r\n                        \r\n                        <!-- Textarea -->\r\n                        <textarea v-else-if=\"setting.type === 'textarea'\" \r\n                                  class=\"form-control\" \r\n                                  :id=\"`setting_${setting.id}`\" \r\n                                  v-model=\"settingValues[setting.key]\"\r\n                                  rows=\"3\"\r\n                                  :disabled=\"loading\"></textarea>\r\n                        \r\n                        <!-- Email -->\r\n                        <input v-else-if=\"setting.type === 'email'\" \r\n                               type=\"email\" \r\n                               class=\"form-control\" \r\n                               :id=\"`setting_${setting.id}`\" \r\n                               v-model=\"settingValues[setting.key]\"\r\n                               :disabled=\"loading\">\r\n                        \r\n                        <!-- URL -->\r\n                        <input v-else-if=\"setting.type === 'url'\" \r\n                               type=\"url\" \r\n                               class=\"form-control\" \r\n                               :id=\"`setting_${setting.id}`\" \r\n                               v-model=\"settingValues[setting.key]\"\r\n                               :disabled=\"loading\">\r\n                        \r\n                        <!-- Text (default) -->\r\n                        <input v-else \r\n                               type=\"text\" \r\n                               class=\"form-control\" \r\n                               :id=\"`setting_${setting.id}`\" \r\n                               v-model=\"settingValues[setting.key]\"\r\n                               :disabled=\"loading\">\r\n                        \r\n                        <!-- Error display -->\r\n                        <div v-if=\"errors[setting.key]\" class=\"invalid-feedback d-block\">\r\n                          {{ errors[setting.key] }}\r\n                        </div>\r\n                      </div>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              \r\n              <!-- Save Button -->\r\n              <div class=\"row\">\r\n                <div class=\"col-12\">\r\n                  <div class=\"card\">\r\n                    <div class=\"card-body text-center\">\r\n                      <button type=\"submit\" \r\n                              class=\"btn btn-primary btn-lg\" \r\n                              :disabled=\"loading\">\r\n                        <i v-if=\"loading\" class=\"fas fa-spinner fa-spin mr-2\"></i>\r\n                        <i v-else class=\"fas fa-save mr-2\"></i>\r\n                        {{ loading ? 'Guardando...' : 'Guardar Configuracin' }}\r\n                      </button>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </form>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </section>\r\n\r\n    <!-- Import Modal -->\r\n    <div class=\"modal fade\" id=\"importModal\" tabindex=\"-1\" role=\"dialog\">\r\n      <div class=\"modal-dialog\" role=\"document\">\r\n        <div class=\"modal-content\">\r\n          <form @submit.prevent=\"importSettings\">\r\n            <div class=\"modal-header\">\r\n              <h4 class=\"modal-title\">\r\n                <i class=\"fas fa-upload mr-2\"></i>\r\n                Importar Configuracin\r\n              </h4>\r\n              <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n                <span>&times;</span>\r\n              </button>\r\n            </div>\r\n            <div class=\"modal-body\">\r\n              <div class=\"form-group\">\r\n                <label for=\"import_file\">Archivo JSON de Configuracin</label>\r\n                <div class=\"custom-file\">\r\n                  <input type=\"file\" \r\n                         class=\"custom-file-input\" \r\n                         id=\"import_file\" \r\n                         ref=\"importFile\"\r\n                         accept=\".json\" \r\n                         @change=\"handleFileSelect\"\r\n                         :disabled=\"importing\">\r\n                  <label class=\"custom-file-label\" for=\"import_file\">\r\n                    {{ selectedFileName || 'Seleccionar archivo...' }}\r\n                  </label>\r\n                </div>\r\n                <small class=\"form-text text-muted\">\r\n                  Solo archivos JSON exportados desde este sistema.\r\n                </small>\r\n              </div>\r\n            </div>\r\n            <div class=\"modal-footer\">\r\n              <button type=\"button\" \r\n                      class=\"btn btn-secondary\" \r\n                      data-dismiss=\"modal\"\r\n                      :disabled=\"importing\">\r\n                Cancelar\r\n              </button>\r\n              <button type=\"submit\" \r\n                      class=\"btn btn-primary\" \r\n                      :disabled=\"!importFile || importing\">\r\n                <i v-if=\"importing\" class=\"fas fa-spinner fa-spin mr-2\"></i>\r\n                <i v-else class=\"fas fa-upload mr-2\"></i>\r\n                {{ importing ? 'Importando...' : 'Importar' }}\r\n              </button>\r\n            </div>\r\n          </form>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Reset Modal -->\r\n    <div class=\"modal fade\" id=\"resetModal\" tabindex=\"-1\" role=\"dialog\">\r\n      <div class=\"modal-dialog\" role=\"document\">\r\n        <div class=\"modal-content\">\r\n          <div class=\"modal-header bg-danger\">\r\n            <h4 class=\"modal-title text-white\">\r\n              <i class=\"fas fa-exclamation-triangle mr-2\"></i>\r\n              Restablecer Configuracin\r\n            </h4>\r\n            <button type=\"button\" class=\"close text-white\" data-dismiss=\"modal\">\r\n              <span>&times;</span>\r\n            </button>\r\n          </div>\r\n          <div class=\"modal-body\">\r\n            <p><strong>Ests seguro?</strong></p>\r\n            <p>Esta accin restablecer todas las configuraciones a sus valores predeterminados.</p>\r\n            <p class=\"text-danger\">\r\n              <i class=\"fas fa-exclamation-triangle mr-1\"></i>\r\n              Esta accin no se puede deshacer.\r\n            </p>\r\n          </div>\r\n          <div class=\"modal-footer\">\r\n            <button type=\"button\" \r\n                    class=\"btn btn-secondary\" \r\n                    data-dismiss=\"modal\"\r\n                    :disabled=\"resetting\">\r\n              Cancelar\r\n            </button>\r\n            <button @click=\"resetSettings\" \r\n                    type=\"button\" \r\n                    class=\"btn btn-danger\"\r\n                    :disabled=\"resetting\">\r\n              <i v-if=\"resetting\" class=\"fas fa-spinner fa-spin mr-2\"></i>\r\n              <i v-else class=\"fas fa-undo mr-2\"></i>\r\n              {{ resetting ? 'Restableciendo...' : 'Restablecer Todo' }}\r\n            </button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'SettingsManager',\r\n  data() {\r\n    return {\r\n      // Estados principales\r\n      loading: false,\r\n      saving: false,\r\n      importing: false,\r\n      resetting: false,\r\n      settingsLoaded: false,\r\n      \r\n      // Datos\r\n      settingsByGroup: {},\r\n      settingValues: {},\r\n      originalValues: {},\r\n      errors: {},\r\n      \r\n      // UI State\r\n      groupCollapsed: {},\r\n      \r\n      // Import\r\n      importFile: null,\r\n      selectedFileName: '',\r\n      \r\n      // Mapeo de grupos\r\n      groupIcons: {\r\n        general: 'fas fa-home',\r\n        system: 'fas fa-server',\r\n        competition: 'fas fa-trophy',\r\n        email: 'fas fa-envelope',\r\n        notifications: 'fas fa-bell',\r\n        payments: 'fas fa-credit-card'\r\n      },\r\n      \r\n      groupTitles: {\r\n        general: 'Configuracin General',\r\n        system: 'Sistema',\r\n        competition: 'Competencias',\r\n        email: 'Email',\r\n        notifications: 'Notificaciones',\r\n        payments: 'Pagos (Flow)'\r\n      }\r\n    }\r\n  },\r\n  \r\n  mounted() {\r\n    this.loadSettings();\r\n  },\r\n  \r\n  methods: {\r\n    async loadSettings() {\r\n      this.loading = true;\r\n      try {\r\n        const response = await fetch('/api/settings');\r\n        if (response.ok) {\r\n          const data = await response.json();\r\n          this.settingsByGroup = data.data;\r\n          this.initializeSettingValues();\r\n          this.initializeGroupStates();\r\n          this.settingsLoaded = true;\r\n        } else {\r\n          this.$refs.notifications.show('Error al cargar configuraciones', 'error');\r\n        }\r\n      } catch (error) {\r\n        console.error('Error:', error);\r\n        this.$refs.notifications.show('Error de conexin', 'error');\r\n      } finally {\r\n        this.loading = false;\r\n      }\r\n    },\r\n    \r\n    initializeSettingValues() {\r\n      this.settingValues = {};\r\n      this.originalValues = {};\r\n      \r\n      Object.values(this.settingsByGroup).forEach(groupSettings => {\r\n        groupSettings.forEach(setting => {\r\n          let value = setting.value;\r\n          \r\n          // Convertir valores segn el tipo\r\n          if (setting.type === 'boolean') {\r\n            value = value === '1' || value === 'true' || value === true;\r\n          } else if (setting.type === 'number' || setting.type === 'integer') {\r\n            value = value ? Number(value) : 0;\r\n          }\r\n          \r\n          this.settingValues[setting.key] = value;\r\n          this.originalValues[setting.key] = value;\r\n        });\r\n      });\r\n    },\r\n    \r\n    initializeGroupStates() {\r\n      this.groupCollapsed = {};\r\n      Object.keys(this.settingsByGroup).forEach(groupName => {\r\n        this.groupCollapsed[groupName] = false;\r\n      });\r\n    },\r\n    \r\n    getGroupIcon(groupName) {\r\n      return this.groupIcons[groupName] || 'fas fa-cog';\r\n    },\r\n    \r\n    getGroupTitle(groupName) {\r\n      return this.groupTitles[groupName] || this.formatTitle(groupName);\r\n    },\r\n    \r\n    formatTitle(str) {\r\n      return str.charAt(0).toUpperCase() + str.slice(1);\r\n    },\r\n    \r\n    formatOptionLabel(option) {\r\n      return option.charAt(0).toUpperCase() + option.slice(1);\r\n    },\r\n    \r\n    toggleGroup(groupName) {\r\n      this.groupCollapsed[groupName] = !this.groupCollapsed[groupName];\r\n    },\r\n    \r\n    async saveSettings() {\r\n      this.loading = true;\r\n      this.errors = {};\r\n      \r\n      try {\r\n        // Preparar datos para envo\r\n        const settingsData = {};\r\n        Object.keys(this.settingValues).forEach(key => {\r\n          let value = this.settingValues[key];\r\n          \r\n          // Convertir boolean a string para el backend\r\n          if (typeof value === 'boolean') {\r\n            value = value ? '1' : '0';\r\n          }\r\n          \r\n          settingsData[key] = value;\r\n        });\r\n        \r\n        const response = await fetch('/api/settings', {\r\n          method: 'PUT',\r\n          headers: {\r\n            'Content-Type': 'application/json',\r\n            'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content')\r\n          },\r\n          body: JSON.stringify({ settings: settingsData })\r\n        });\r\n        \r\n        const result = await response.json();\r\n        \r\n        if (response.ok) {\r\n          this.originalValues = { ...this.settingValues };\r\n          this.$refs.notifications.show('Configuraciones guardadas correctamente', 'success');\r\n        } else {\r\n          if (result.errors) {\r\n            this.errors = result.errors;\r\n          }\r\n          this.$refs.notifications.show(result.message || 'Error al guardar configuraciones', 'error');\r\n        }\r\n      } catch (error) {\r\n        console.error('Error:', error);\r\n        this.$refs.notifications.show('Error de conexin', 'error');\r\n      } finally {\r\n        this.loading = false;\r\n      }\r\n    },\r\n    \r\n    async exportSettings() {\r\n      try {\r\n        const response = await fetch('/api/settings/export');\r\n        if (response.ok) {\r\n          const blob = await response.blob();\r\n          const url = window.URL.createObjectURL(blob);\r\n          const a = document.createElement('a');\r\n          a.href = url;\r\n          a.download = `configuraciones_${new Date().toISOString().slice(0, 19).replace(/:/g, '-')}.json`;\r\n          a.click();\r\n          window.URL.revokeObjectURL(url);\r\n          this.$refs.notifications.show('Configuraciones exportadas correctamente', 'success');\r\n        } else {\r\n          this.$refs.notifications.show('Error al exportar configuraciones', 'error');\r\n        }\r\n      } catch (error) {\r\n        console.error('Error:', error);\r\n        this.$refs.notifications.show('Error de conexin', 'error');\r\n      }\r\n    },\r\n    \r\n    showImportModal() {\r\n      $('#importModal').modal('show');\r\n    },\r\n    \r\n    showResetModal() {\r\n      $('#resetModal').modal('show');\r\n    },\r\n    \r\n    handleFileSelect(event) {\r\n      const file = event.target.files[0];\r\n      if (file) {\r\n        this.importFile = file;\r\n        this.selectedFileName = file.name;\r\n      } else {\r\n        this.importFile = null;\r\n        this.selectedFileName = '';\r\n      }\r\n    },\r\n    \r\n    async importSettings() {\r\n      if (!this.importFile) return;\r\n      \r\n      this.importing = true;\r\n      \r\n      try {\r\n        const formData = new FormData();\r\n        formData.append('file', this.importFile);\r\n        \r\n        const response = await fetch('/api/settings/import', {\r\n          method: 'POST',\r\n          headers: {\r\n            'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content')\r\n          },\r\n          body: formData\r\n        });\r\n        \r\n        const result = await response.json();\r\n        \r\n        if (response.ok) {\r\n          $('#importModal').modal('hide');\r\n          this.loadSettings(); // Recargar configuraciones\r\n          this.$refs.notifications.show('Configuraciones importadas correctamente', 'success');\r\n        } else {\r\n          this.$refs.notifications.show(result.message || 'Error al importar configuraciones', 'error');\r\n        }\r\n      } catch (error) {\r\n        console.error('Error:', error);\r\n        this.$refs.notifications.show('Error de conexin', 'error');\r\n      } finally {\r\n        this.importing = false;\r\n        this.importFile = null;\r\n        this.selectedFileName = '';\r\n        if (this.$refs.importFile) {\r\n          this.$refs.importFile.value = '';\r\n        }\r\n      }\r\n    },\r\n    \r\n    async resetSettings() {\r\n      this.resetting = true;\r\n      \r\n      try {\r\n        const response = await fetch('/api/settings/reset', {\r\n          method: 'POST',\r\n          headers: {\r\n            'Content-Type': 'application/json',\r\n            'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content')\r\n          }\r\n        });\r\n        \r\n        const result = await response.json();\r\n        \r\n        if (response.ok) {\r\n          $('#resetModal').modal('hide');\r\n          this.loadSettings(); // Recargar configuraciones\r\n          this.$refs.notifications.show('Configuraciones restablecidas correctamente', 'success');\r\n        } else {\r\n          this.$refs.notifications.show(result.message || 'Error al restablecer configuraciones', 'error');\r\n        }\r\n      } catch (error) {\r\n        console.error('Error:', error);\r\n        this.$refs.notifications.show('Error de conexin', 'error');\r\n      } finally {\r\n        this.resetting = false;\r\n      }\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.card-tools .dropdown-menu {\r\n  border: 1px solid #dee2e6;\r\n  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);\r\n}\r\n\r\n.form-group label {\r\n  font-weight: 600;\r\n  color: #495057;\r\n}\r\n\r\n.custom-control-label::before {\r\n  border-radius: 0.375rem;\r\n}\r\n\r\n.custom-control-input:checked ~ .custom-control-label::before {\r\n  background-color: #007bff;\r\n  border-color: #007bff;\r\n}\r\n\r\n.invalid-feedback {\r\n  font-size: 0.875em;\r\n}\r\n\r\n.modal-header.bg-danger .close {\r\n  opacity: 0.8;\r\n}\r\n\r\n.modal-header.bg-danger .close:hover {\r\n  opacity: 1;\r\n}\r\n\r\n.custom-file-label.selected {\r\n  color: #495057;\r\n}\r\n\r\n.dropdown-item:disabled {\r\n  color: #6c757d;\r\n  pointer-events: none;\r\n  background-color: transparent;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
