___CSS_LOADER_EXPORT___.push([module.id, ".info-box[data-v-5d0935a2]{margin-bottom:15px}.modal-lg[data-v-5d0935a2]{max-width:800px}.pagination[data-v-5d0935a2]{margin:0}.btn-group .btn[data-v-5d0935a2]{margin-right:2px}.btn-group .btn[data-v-5d0935a2]:last-child{margin-right:0}.card-outline[data-v-5d0935a2]{border-width:2px}.dropdown-menu[data-v-5d0935a2]{min-width:150px}", "",{"version":3,"sources":["webpack://./resources/js/components/UserManager.vue"],"names":[],"mappings":"AAguBA,2BACA,kBACA,CAEA,2BACA,eACA,CAEA,6BACA,QACA,CAEA,iCACA,gBACA,CAEA,4CACA,cACA,CAEA,+BACA,gBACA,CAEA,gCACA,eACA","sourcesContent":["<template>\r\n  <div class=\"container-fluid\">\r\n    <!-- Header -->\r\n    <div class=\"content-header\">\r\n      <div class=\"container-fluid\">\r\n        <div class=\"row mb-2\">\r\n          <div class=\"col-sm-6\">\r\n            <h1 class=\"m-0\">\r\n              <i class=\"fas fa-users text-primary\"></i>\r\n              Gestin de Usuarios\r\n            </h1>\r\n          </div>\r\n          <div class=\"col-sm-6\">\r\n            <ol class=\"breadcrumb float-sm-right\">\r\n              <li class=\"breadcrumb-item\"><a href=\"/dashboard\">Dashboard</a></li>\r\n              <li class=\"breadcrumb-item active\">Usuarios</li>\r\n            </ol>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- Content -->\r\n    <section class=\"content\">\r\n      <div class=\"container-fluid\">\r\n        <div class=\"row\">\r\n          <div class=\"col-12\">\r\n            <div class=\"card\">\r\n              <div class=\"card-header\">\r\n                <h3 class=\"card-title\">\r\n                  <i class=\"fas fa-users mr-2\"></i>\r\n                  Lista de Usuarios del Sistema\r\n                </h3>\r\n                <div class=\"card-tools\">\r\n                  <div class=\"btn-group mr-2\" role=\"group\">\r\n                    <button \r\n                      type=\"button\" \r\n                      class=\"btn btn-outline-primary btn-sm\"\r\n                      :class=\"{ active: viewMode === 'table' }\"\r\n                      @click=\"viewMode = 'table'\"\r\n                    >\r\n                      <i class=\"fas fa-table\"></i> Tabla\r\n                    </button>\r\n                    <button \r\n                      type=\"button\" \r\n                      class=\"btn btn-outline-primary btn-sm\"\r\n                      :class=\"{ active: viewMode === 'cards' }\"\r\n                      @click=\"viewMode = 'cards'\"\r\n                    >\r\n                      <i class=\"fas fa-th-large\"></i> Cards\r\n                    </button>\r\n                  </div>\r\n                  <button @click=\"showCreateModal\" class=\"btn btn-primary btn-sm\">\r\n                    <i class=\"fas fa-plus\"></i> Nuevo Usuario\r\n                  </button>\r\n                </div>\r\n              </div>\r\n\r\n              <!-- Search and Filters -->\r\n              <div class=\"card-body border-bottom\">\r\n                <div class=\"row\">\r\n                  <div class=\"col-md-4\">\r\n                    <div class=\"form-group\">\r\n                      <label>Buscar usuarios:</label>\r\n                      <input \r\n                        v-model=\"searchQuery\" \r\n                        @input=\"searchUsers\"\r\n                        type=\"text\" \r\n                        class=\"form-control\" \r\n                        placeholder=\"Nombre o email...\"\r\n                      >\r\n                    </div>\r\n                  </div>\r\n                  <div class=\"col-md-3\">\r\n                    <div class=\"form-group\">\r\n                      <label>Estado de verificacin:</label>\r\n                      <select v-model=\"filters.verification\" @change=\"loadUsers\" class=\"form-control\">\r\n                        <option value=\"\">Todos</option>\r\n                        <option value=\"verified\">Verificados</option>\r\n                        <option value=\"unverified\">No verificados</option>\r\n                      </select>\r\n                    </div>\r\n                  </div>\r\n                  <div class=\"col-md-3\">\r\n                    <div class=\"form-group\">\r\n                      <label>Ordenar por:</label>\r\n                      <select v-model=\"sortBy\" @change=\"loadUsers\" class=\"form-control\">\r\n                        <option value=\"name\">Nombre</option>\r\n                        <option value=\"email\">Email</option>\r\n                        <option value=\"created_at\">Fecha de registro</option>\r\n                      </select>\r\n                    </div>\r\n                  </div>\r\n                  <div class=\"col-md-2\">\r\n                    <div class=\"form-group\">\r\n                      <label>Orden:</label>\r\n                      <select v-model=\"sortOrder\" @change=\"loadUsers\" class=\"form-control\">\r\n                        <option value=\"asc\">Ascendente</option>\r\n                        <option value=\"desc\">Descendente</option>\r\n                      </select>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n\r\n              <!-- Loading State -->\r\n              <div v-if=\"loading\" class=\"card-body text-center\">\r\n                <div class=\"spinner-border text-primary\" role=\"status\">\r\n                  <span class=\"sr-only\">Cargando...</span>\r\n                </div>\r\n                <p class=\"mt-2\">Cargando usuarios...</p>\r\n              </div>\r\n\r\n              <!-- Table View -->\r\n              <div v-else-if=\"viewMode === 'table'\" class=\"card-body table-responsive p-0\">\r\n                <table v-if=\"users.length > 0\" class=\"table table-hover text-nowrap\">\r\n                  <thead>\r\n                    <tr>\r\n                      <th>ID</th>\r\n                      <th>Nombre</th>\r\n                      <th>Email</th>\r\n                      <th>Verificado</th>\r\n                      <th>Registrado</th>\r\n                      <th>ltimo acceso</th>\r\n                      <th>Acciones</th>\r\n                    </tr>\r\n                  </thead>\r\n                  <tbody>\r\n                    <tr v-for=\"user in users\" :key=\"user.id\">\r\n                      <td>{{ user.id }}</td>\r\n                      <td>\r\n                        <strong>{{ user.name }}</strong>\r\n                        <span v-if=\"isCurrentUser(user.id)\" class=\"badge badge-success ml-1\">T</span>\r\n                      </td>\r\n                      <td>{{ user.email }}</td>\r\n                      <td>\r\n                        <span v-if=\"user.email_verified_at\" class=\"badge badge-success\">\r\n                          <i class=\"fas fa-check\"></i> Verificado\r\n                        </span>\r\n                        <span v-else class=\"badge badge-warning\">\r\n                          <i class=\"fas fa-clock\"></i> Pendiente\r\n                        </span>\r\n                      </td>\r\n                      <td>{{ formatDate(user.created_at) }}</td>\r\n                      <td>{{ formatDate(user.last_login_at) || 'Nunca' }}</td>\r\n                      <td>\r\n                        <div class=\"btn-group\">\r\n                          <button @click=\"viewUser(user)\" class=\"btn btn-info btn-sm\" title=\"Ver detalles\">\r\n                            <i class=\"fas fa-eye\"></i>\r\n                          </button>\r\n                          <button @click=\"editUser(user)\" class=\"btn btn-warning btn-sm\" title=\"Editar\">\r\n                            <i class=\"fas fa-edit\"></i>\r\n                          </button>\r\n                          <button \r\n                            v-if=\"!isCurrentUser(user.id)\" \r\n                            @click=\"deleteUser(user)\" \r\n                            class=\"btn btn-danger btn-sm\" \r\n                            title=\"Eliminar\"\r\n                          >\r\n                            <i class=\"fas fa-trash\"></i>\r\n                          </button>\r\n                        </div>\r\n                      </td>\r\n                    </tr>\r\n                  </tbody>\r\n                </table>\r\n                <div v-else class=\"text-center py-4\">\r\n                  <i class=\"fas fa-users fa-3x text-muted mb-3\"></i>\r\n                  <h5 class=\"text-muted\">No se encontraron usuarios</h5>\r\n                  <p class=\"text-muted\">{{ searchQuery ? 'Intenta con otros trminos de bsqueda' : 'Comienza creando el primer usuario del sistema' }}</p>\r\n                </div>\r\n              </div>\r\n\r\n              <!-- Cards View -->\r\n              <div v-else-if=\"viewMode === 'cards'\" class=\"card-body\">\r\n                <div v-if=\"users.length > 0\" class=\"row\">\r\n                  <div v-for=\"user in users\" :key=\"user.id\" class=\"col-md-6 col-lg-4 mb-3\">\r\n                    <div class=\"card card-outline\" :class=\"isCurrentUser(user.id) ? 'card-success' : 'card-primary'\">\r\n                      <div class=\"card-body\">\r\n                        <div class=\"d-flex justify-content-between align-items-start mb-2\">\r\n                          <h5 class=\"card-title mb-0\">\r\n                            {{ user.name }}\r\n                            <span v-if=\"isCurrentUser(user.id)\" class=\"badge badge-success ml-1\">T</span>\r\n                          </h5>\r\n                          <div class=\"dropdown\">\r\n                            <button class=\"btn btn-sm btn-outline-secondary dropdown-toggle\" data-toggle=\"dropdown\">\r\n                              <i class=\"fas fa-ellipsis-v\"></i>\r\n                            </button>\r\n                            <div class=\"dropdown-menu dropdown-menu-right\">\r\n                              <button @click=\"viewUser(user)\" class=\"dropdown-item\">\r\n                                <i class=\"fas fa-eye mr-2\"></i> Ver detalles\r\n                              </button>\r\n                              <button @click=\"editUser(user)\" class=\"dropdown-item\">\r\n                                <i class=\"fas fa-edit mr-2\"></i> Editar\r\n                              </button>\r\n                              <div v-if=\"!isCurrentUser(user.id)\" class=\"dropdown-divider\"></div>\r\n                              <button \r\n                                v-if=\"!isCurrentUser(user.id)\" \r\n                                @click=\"deleteUser(user)\" \r\n                                class=\"dropdown-item text-danger\"\r\n                              >\r\n                                <i class=\"fas fa-trash mr-2\"></i> Eliminar\r\n                              </button>\r\n                            </div>\r\n                          </div>\r\n                        </div>\r\n                        <p class=\"card-text\">\r\n                          <i class=\"fas fa-envelope mr-1\"></i>{{ user.email }}\r\n                        </p>\r\n                        <div class=\"d-flex justify-content-between align-items-center\">\r\n                          <span v-if=\"user.email_verified_at\" class=\"badge badge-success\">\r\n                            <i class=\"fas fa-check\"></i> Verificado\r\n                          </span>\r\n                          <span v-else class=\"badge badge-warning\">\r\n                            <i class=\"fas fa-clock\"></i> Pendiente\r\n                          </span>\r\n                          <small class=\"text-muted\">{{ formatDate(user.created_at) }}</small>\r\n                        </div>\r\n                      </div>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n                <div v-else class=\"text-center py-4\">\r\n                  <i class=\"fas fa-users fa-3x text-muted mb-3\"></i>\r\n                  <h5 class=\"text-muted\">No se encontraron usuarios</h5>\r\n                  <p class=\"text-muted\">{{ searchQuery ? 'Intenta con otros trminos de bsqueda' : 'Comienza creando el primer usuario del sistema' }}</p>\r\n                </div>\r\n              </div>\r\n\r\n              <!-- Pagination -->\r\n              <div v-if=\"pagination.total > pagination.per_page\" class=\"card-footer\">\r\n                <nav>\r\n                  <ul class=\"pagination justify-content-center mb-0\">\r\n                    <li class=\"page-item\" :class=\"{ disabled: pagination.current_page === 1 }\">\r\n                      <button @click=\"changePage(pagination.current_page - 1)\" class=\"page-link\" :disabled=\"pagination.current_page === 1\">\r\n                        Anterior\r\n                      </button>\r\n                    </li>\r\n                    <li v-for=\"page in visiblePages\" :key=\"page\" class=\"page-item\" :class=\"{ active: page === pagination.current_page }\">\r\n                      <button @click=\"changePage(page)\" class=\"page-link\">{{ page }}</button>\r\n                    </li>\r\n                    <li class=\"page-item\" :class=\"{ disabled: pagination.current_page === pagination.last_page }\">\r\n                      <button @click=\"changePage(pagination.current_page + 1)\" class=\"page-link\" :disabled=\"pagination.current_page === pagination.last_page\">\r\n                        Siguiente\r\n                      </button>\r\n                    </li>\r\n                  </ul>\r\n                </nav>\r\n              </div>\r\n            </div>\r\n\r\n            <!-- Statistics Card -->\r\n            <div class=\"row mt-3\">\r\n              <div class=\"col-md-3\">\r\n                <div class=\"info-box\">\r\n                  <span class=\"info-box-icon bg-info\"><i class=\"fas fa-users\"></i></span>\r\n                  <div class=\"info-box-content\">\r\n                    <span class=\"info-box-text\">Total Usuarios</span>\r\n                    <span class=\"info-box-number\">{{ statistics.total || 0 }}</span>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-3\">\r\n                <div class=\"info-box\">\r\n                  <span class=\"info-box-icon bg-success\"><i class=\"fas fa-user-check\"></i></span>\r\n                  <div class=\"info-box-content\">\r\n                    <span class=\"info-box-text\">Verificados</span>\r\n                    <span class=\"info-box-number\">{{ statistics.verified || 0 }}</span>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-3\">\r\n                <div class=\"info-box\">\r\n                  <span class=\"info-box-icon bg-warning\"><i class=\"fas fa-user-clock\"></i></span>\r\n                  <div class=\"info-box-content\">\r\n                    <span class=\"info-box-text\">Pendientes</span>\r\n                    <span class=\"info-box-number\">{{ statistics.unverified || 0 }}</span>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"col-md-3\">\r\n                <div class=\"info-box\">\r\n                  <span class=\"info-box-icon bg-primary\"><i class=\"fas fa-user-plus\"></i></span>\r\n                  <div class=\"info-box-content\">\r\n                    <span class=\"info-box-text\">Este mes</span>\r\n                    <span class=\"info-box-number\">{{ statistics.this_month || 0 }}</span>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </section>\r\n\r\n    <!-- Create/Edit User Modal -->\r\n    <div class=\"modal fade\" id=\"userModal\" tabindex=\"-1\" role=\"dialog\">\r\n      <div class=\"modal-dialog modal-lg\" role=\"document\">\r\n        <div class=\"modal-content\">\r\n          <div class=\"modal-header\">\r\n            <h5 class=\"modal-title\">\r\n              {{ editingUser ? 'Editar Usuario' : 'Nuevo Usuario' }}\r\n            </h5>\r\n            <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n              <span>&times;</span>\r\n            </button>\r\n          </div>\r\n          <div class=\"modal-body\">\r\n            <form @submit.prevent=\"saveUser\">\r\n              <div class=\"row\">\r\n                <div class=\"col-md-6\">\r\n                  <div class=\"form-group\">\r\n                    <label>Nombre <span class=\"text-danger\">*</span></label>\r\n                    <input \r\n                      v-model=\"userForm.name\" \r\n                      type=\"text\" \r\n                      class=\"form-control\" \r\n                      :class=\"{ 'is-invalid': errors.name }\"\r\n                      required\r\n                    >\r\n                    <div v-if=\"errors.name\" class=\"invalid-feedback\">{{ errors.name[0] }}</div>\r\n                  </div>\r\n                </div>\r\n                <div class=\"col-md-6\">\r\n                  <div class=\"form-group\">\r\n                    <label>Email <span class=\"text-danger\">*</span></label>\r\n                    <input \r\n                      v-model=\"userForm.email\" \r\n                      type=\"email\" \r\n                      class=\"form-control\" \r\n                      :class=\"{ 'is-invalid': errors.email }\"\r\n                      required\r\n                    >\r\n                    <div v-if=\"errors.email\" class=\"invalid-feedback\">{{ errors.email[0] }}</div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"row\" v-if=\"!editingUser\">\r\n                <div class=\"col-md-6\">\r\n                  <div class=\"form-group\">\r\n                    <label>Contrasea <span class=\"text-danger\">*</span></label>\r\n                    <input \r\n                      v-model=\"userForm.password\" \r\n                      type=\"password\" \r\n                      class=\"form-control\" \r\n                      :class=\"{ 'is-invalid': errors.password }\"\r\n                      :required=\"!editingUser\"\r\n                    >\r\n                    <div v-if=\"errors.password\" class=\"invalid-feedback\">{{ errors.password[0] }}</div>\r\n                  </div>\r\n                </div>\r\n                <div class=\"col-md-6\">\r\n                  <div class=\"form-group\">\r\n                    <label>Confirmar Contrasea <span class=\"text-danger\">*</span></label>\r\n                    <input \r\n                      v-model=\"userForm.password_confirmation\" \r\n                      type=\"password\" \r\n                      class=\"form-control\"\r\n                      :required=\"!editingUser\"\r\n                    >\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"row\" v-if=\"editingUser\">\r\n                <div class=\"col-12\">\r\n                  <div class=\"form-group\">\r\n                    <div class=\"custom-control custom-checkbox\">\r\n                      <input \r\n                        v-model=\"userForm.change_password\" \r\n                        type=\"checkbox\" \r\n                        class=\"custom-control-input\" \r\n                        id=\"changePassword\"\r\n                      >\r\n                      <label class=\"custom-control-label\" for=\"changePassword\">\r\n                        Cambiar contrasea\r\n                      </label>\r\n                    </div>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"row\" v-if=\"editingUser && userForm.change_password\">\r\n                <div class=\"col-md-6\">\r\n                  <div class=\"form-group\">\r\n                    <label>Nueva Contrasea <span class=\"text-danger\">*</span></label>\r\n                    <input \r\n                      v-model=\"userForm.password\" \r\n                      type=\"password\" \r\n                      class=\"form-control\" \r\n                      :class=\"{ 'is-invalid': errors.password }\"\r\n                    >\r\n                    <div v-if=\"errors.password\" class=\"invalid-feedback\">{{ errors.password[0] }}</div>\r\n                  </div>\r\n                </div>\r\n                <div class=\"col-md-6\">\r\n                  <div class=\"form-group\">\r\n                    <label>Confirmar Nueva Contrasea <span class=\"text-danger\">*</span></label>\r\n                    <input \r\n                      v-model=\"userForm.password_confirmation\" \r\n                      type=\"password\" \r\n                      class=\"form-control\"\r\n                    >\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </form>\r\n          </div>\r\n          <div class=\"modal-footer\">\r\n            <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Cancelar</button>\r\n            <button @click=\"saveUser\" type=\"button\" class=\"btn btn-primary\" :disabled=\"saving\">\r\n              <i v-if=\"saving\" class=\"fas fa-spinner fa-spin mr-1\"></i>\r\n              {{ editingUser ? 'Actualizar' : 'Crear' }} Usuario\r\n            </button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n\r\n    <!-- View User Modal -->\r\n    <div class=\"modal fade\" id=\"viewUserModal\" tabindex=\"-1\" role=\"dialog\">\r\n      <div class=\"modal-dialog modal-lg\" role=\"document\">\r\n        <div class=\"modal-content\">\r\n          <div class=\"modal-header\">\r\n            <h5 class=\"modal-title\">Detalles del Usuario</h5>\r\n            <button type=\"button\" class=\"close\" data-dismiss=\"modal\">\r\n              <span>&times;</span>\r\n            </button>\r\n          </div>\r\n          <div class=\"modal-body\" v-if=\"selectedUser\">\r\n            <div class=\"row\">\r\n              <div class=\"col-md-6\">\r\n                <table class=\"table table-borderless\">\r\n                  <tr>\r\n                    <th>ID:</th>\r\n                    <td>{{ selectedUser.id }}</td>\r\n                  </tr>\r\n                  <tr>\r\n                    <th>Nombre:</th>\r\n                    <td>{{ selectedUser.name }}</td>\r\n                  </tr>\r\n                  <tr>\r\n                    <th>Email:</th>\r\n                    <td>{{ selectedUser.email }}</td>\r\n                  </tr>\r\n                  <tr>\r\n                    <th>Estado:</th>\r\n                    <td>\r\n                      <span v-if=\"selectedUser.email_verified_at\" class=\"badge badge-success\">\r\n                        <i class=\"fas fa-check\"></i> Verificado\r\n                      </span>\r\n                      <span v-else class=\"badge badge-warning\">\r\n                        <i class=\"fas fa-clock\"></i> Pendiente\r\n                      </span>\r\n                    </td>\r\n                  </tr>\r\n                </table>\r\n              </div>\r\n              <div class=\"col-md-6\">\r\n                <table class=\"table table-borderless\">\r\n                  <tr>\r\n                    <th>Registrado:</th>\r\n                    <td>{{ formatDate(selectedUser.created_at) }}</td>\r\n                  </tr>\r\n                  <tr>\r\n                    <th>Actualizado:</th>\r\n                    <td>{{ formatDate(selectedUser.updated_at) }}</td>\r\n                  </tr>\r\n                  <tr>\r\n                    <th>ltimo acceso:</th>\r\n                    <td>{{ formatDate(selectedUser.last_login_at) || 'Nunca' }}</td>\r\n                  </tr>\r\n                  <tr v-if=\"selectedUser.email_verified_at\">\r\n                    <th>Verificado:</th>\r\n                    <td>{{ formatDate(selectedUser.email_verified_at) }}</td>\r\n                  </tr>\r\n                </table>\r\n              </div>\r\n            </div>\r\n          </div>\r\n          <div class=\"modal-footer\">\r\n            <button type=\"button\" class=\"btn btn-secondary\" data-dismiss=\"modal\">Cerrar</button>\r\n            <button @click=\"editUser(selectedUser)\" type=\"button\" class=\"btn btn-warning\">\r\n              <i class=\"fas fa-edit\"></i> Editar\r\n            </button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: 'UserManager',\r\n  data() {\r\n    return {\r\n      users: [],\r\n      loading: true,\r\n      saving: false,\r\n      searchQuery: '',\r\n      viewMode: 'table',\r\n      sortBy: 'name',\r\n      sortOrder: 'asc',\r\n      filters: {\r\n        verification: ''\r\n      },\r\n      pagination: {\r\n        current_page: 1,\r\n        last_page: 1,\r\n        per_page: 20,\r\n        total: 0\r\n      },\r\n      statistics: {},\r\n      editingUser: null,\r\n      selectedUser: null,\r\n      userForm: {\r\n        name: '',\r\n        email: '',\r\n        password: '',\r\n        password_confirmation: '',\r\n        change_password: false\r\n      },\r\n      errors: {},\r\n      currentUserId: null,\r\n      searchTimeout: null\r\n    }\r\n  },\r\n  computed: {\r\n    visiblePages() {\r\n      const pages = [];\r\n      const current = this.pagination.current_page;\r\n      const last = this.pagination.last_page;\r\n      \r\n      for (let i = Math.max(1, current - 2); i <= Math.min(last, current + 2); i++) {\r\n        pages.push(i);\r\n      }\r\n      \r\n      return pages;\r\n    }\r\n  },\r\n  async mounted() {\r\n    await this.loadCurrentUser();\r\n    await this.loadUsers();\r\n    await this.loadStatistics();\r\n  },\r\n  methods: {\r\n    async loadCurrentUser() {\r\n      try {\r\n        const response = await axios.get('/api/user');\r\n        this.currentUserId = response.data.id;\r\n      } catch (error) {\r\n        console.error('Error loading current user:', error);\r\n      }\r\n    },\r\n\r\n    async loadUsers(page = 1) {\r\n      this.loading = true;\r\n      try {\r\n        const params = {\r\n          page: page,\r\n          search: this.searchQuery,\r\n          sort_by: this.sortBy,\r\n          sort_order: this.sortOrder,\r\n          verification: this.filters.verification\r\n        };\r\n\r\n        const response = await axios.get('/gestionar/api/usuarios', { params });\r\n        \r\n        if (response.data.success) {\r\n          this.users = response.data.data.data;\r\n          this.pagination = {\r\n            current_page: response.data.data.current_page,\r\n            last_page: response.data.data.last_page,\r\n            per_page: response.data.data.per_page,\r\n            total: response.data.data.total\r\n          };\r\n        }\r\n      } catch (error) {\r\n        console.error('Error loading users:', error);\r\n        this.showNotification('Error al cargar usuarios', 'error');\r\n      } finally {\r\n        this.loading = false;\r\n      }\r\n    },\r\n\r\n    async loadStatistics() {\r\n      try {\r\n        const response = await axios.get('/gestionar/api/usuarios/estadisticas');\r\n        if (response.data.success) {\r\n          this.statistics = response.data.data;\r\n        }\r\n      } catch (error) {\r\n        console.error('Error loading statistics:', error);\r\n      }\r\n    },\r\n\r\n    searchUsers() {\r\n      clearTimeout(this.searchTimeout);\r\n      this.searchTimeout = setTimeout(() => {\r\n        this.loadUsers(1);\r\n      }, 300);\r\n    },\r\n\r\n    changePage(page) {\r\n      if (page >= 1 && page <= this.pagination.last_page) {\r\n        this.loadUsers(page);\r\n      }\r\n    },\r\n\r\n    showCreateModal() {\r\n      this.editingUser = null;\r\n      this.userForm = {\r\n        name: '',\r\n        email: '',\r\n        password: '',\r\n        password_confirmation: '',\r\n        change_password: false\r\n      };\r\n      this.errors = {};\r\n      $('#userModal').modal('show');\r\n    },\r\n\r\n    editUser(user) {\r\n      this.editingUser = user;\r\n      this.userForm = {\r\n        name: user.name,\r\n        email: user.email,\r\n        password: '',\r\n        password_confirmation: '',\r\n        change_password: false\r\n      };\r\n      this.errors = {};\r\n      $('#viewUserModal').modal('hide');\r\n      $('#userModal').modal('show');\r\n    },\r\n\r\n    viewUser(user) {\r\n      this.selectedUser = user;\r\n      $('#viewUserModal').modal('show');\r\n    },\r\n\r\n    async saveUser() {\r\n      this.saving = true;\r\n      this.errors = {};\r\n\r\n      try {\r\n        const url = this.editingUser \r\n          ? `/gestionar/api/usuarios/${this.editingUser.id}`\r\n          : '/gestionar/api/usuarios';\r\n        \r\n        const method = this.editingUser ? 'put' : 'post';\r\n        \r\n        const formData = { ...this.userForm };\r\n        if (this.editingUser && !formData.change_password) {\r\n          delete formData.password;\r\n          delete formData.password_confirmation;\r\n        }\r\n        delete formData.change_password;\r\n\r\n        const response = await axios[method](url, formData);\r\n        \r\n        if (response.data.success) {\r\n          this.showNotification(\r\n            this.editingUser ? 'Usuario actualizado exitosamente' : 'Usuario creado exitosamente',\r\n            'success'\r\n          );\r\n          $('#userModal').modal('hide');\r\n          await this.loadUsers(this.pagination.current_page);\r\n          await this.loadStatistics();\r\n        }\r\n      } catch (error) {\r\n        if (error.response && error.response.status === 422) {\r\n          this.errors = error.response.data.errors || {};\r\n        } else {\r\n          this.showNotification('Error al guardar usuario', 'error');\r\n        }\r\n      } finally {\r\n        this.saving = false;\r\n      }\r\n    },\r\n\r\n    async deleteUser(user) {\r\n      if (!confirm(`Ests seguro de eliminar al usuario \"${user.name}\"?`)) {\r\n        return;\r\n      }\r\n\r\n      try {\r\n        const response = await axios.delete(`/gestionar/api/usuarios/${user.id}`);\r\n        \r\n        if (response.data.success) {\r\n          this.showNotification('Usuario eliminado exitosamente', 'success');\r\n          await this.loadUsers(this.pagination.current_page);\r\n          await this.loadStatistics();\r\n        }\r\n      } catch (error) {\r\n        this.showNotification('Error al eliminar usuario', 'error');\r\n      }\r\n    },\r\n\r\n    isCurrentUser(userId) {\r\n      return userId === this.currentUserId;\r\n    },\r\n\r\n    formatDate(date) {\r\n      if (!date) return null;\r\n      return new Date(date).toLocaleString('es-ES', {\r\n        year: 'numeric',\r\n        month: '2-digit',\r\n        day: '2-digit',\r\n        hour: '2-digit',\r\n        minute: '2-digit'\r\n      });\r\n    },\r\n\r\n    showNotification(message, type = 'info') {\r\n      // Implementar sistema de notificaciones\r\n      const alertClass = type === 'error' ? 'danger' : type;\r\n      const notification = $(`\r\n        <div class=\"alert alert-${alertClass} alert-dismissible fade show\" role=\"alert\">\r\n          ${message}\r\n          <button type=\"button\" class=\"close\" data-dismiss=\"alert\">\r\n            <span aria-hidden=\"true\">&times;</span>\r\n          </button>\r\n        </div>\r\n      `);\r\n      \r\n      $('.content-header').after(notification);\r\n      \r\n      setTimeout(() => {\r\n        notification.fadeOut();\r\n      }, 5000);\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.info-box {\r\n  margin-bottom: 15px;\r\n}\r\n\r\n.modal-lg {\r\n  max-width: 800px;\r\n}\r\n\r\n.pagination {\r\n  margin: 0;\r\n}\r\n\r\n.btn-group .btn {\r\n  margin-right: 2px;\r\n}\r\n\r\n.btn-group .btn:last-child {\r\n  margin-right: 0;\r\n}\r\n\r\n.card-outline {\r\n  border-width: 2px;\r\n}\r\n\r\n.dropdown-menu {\r\n  min-width: 150px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
